<template>
    <div class="b_wrap">
        <div class="b_article">
            <div class="b_article_header">
                <div class="b_article_header_col">{{state.result.nickname}}</div>
                <div class="b_article_header_col">{{moment(state.result.regdate).format("YYYY-MM-DD HH:mm:ss")}}</div>
                <div class="b_article_header_right">
                    <label style="margin-right: 15px">조회수 : {{state.result.hit}}</label>
                    <label>추천 : {{state.result.boardlike}}</label>
                </div>
            </div>
            <div class="b_article_title">
                <h3>{{state.title}}</h3>
            </div>
            <div id="board" class="b_article_content">
            </div>
            <div class="b_article_content_footer">
                <q-btn class="b_article_content_footer_btn" icon="thumb_up" size="18px" @click="handlelike" :label="state.result.boardlike" />
            </div>
            <div class="b_article_side">
                <q-btn flat label="신고하기"></q-btn>
                <q-btn flat label="스크랩"></q-btn>
            </div>
            <div class="b_article_footer">
                <div class="b_article_footer_leftbtn">
                    <q-btn flat icon="navigate_before"  @click="handlePrev" label="이전글" />                    
                    <q-btn flat icon-right="navigate_next" @click="handleNext" label="다음글" />                    
                    <q-btn flat @click="handlelist" label="목록" />                    
                </div>
                <div class="b_article_footer_rightbtn">
                    <q-btn flat v-if="state.nickname === state.id" @click="handleupdate" label="수정" />                    
                    <q-btn flat v-if="state.nickname === state.id" @click="handledelete" label="삭제" />                    
                </div>
            </div>
            <div id="boardreply">
                <div class="b_reply_title">
                    <q-icon class="b_reply_icon" name="comment" size="1.5rem"></q-icon>
                    <label>댓글</label>
                    <label>({{state.reptotal}})</label>
                </div>
                <div v-for="tmp of state.boardreply" :key="tmp" >
                    <div v-if="state.upreplyno != tmp.no">
                        <div class="b_reply_header">
                            <div>
                                {{tmp.nickname}}({{moment(tmp.regdate).format("YYYY-MM-DD HH:mm:ss")}})
                            </div>
                            <div>
                                <q-btn flat class="b_reply_header_icon_edit" v-if="tmp.nickname === state.id" @click="handleupdatereply(tmp)" icon="edit" />                    
                                <q-btn flat class="b_reply_header_icon_del" v-if="tmp.nickname === state.id" @click="handledeletereply(tmp)" icon="delete" />                    
                            </div>
                        </div>
                        <div class="b_reply_article">
                            <br />
                            {{tmp.content}}
                        </div>
                    </div>
                    <div v-if="state.upreplyno == tmp.no">
                        <div class="b_reply_update">
                            <textarea class="b_reply_update_input" v-model=state.upreply rows="4" cols="105"></textarea>
                            <div class="b_reply_update_btn">
                                <q-btn class="b_reply_update_btn_update" outline @click="handlerepupdate" icon="edit" size="20px" />
                                <q-btn class="b_reply_update_btn_del" outline @click="cancelrepupdate" icon="cancel" size="20px" />
                            </div>
                        </div>
                    </div>
                </div>
                <div class="q-pa-lg flex flex-center">
                    <q-pagination flat v-model="state.page" :max=state.total @click="handleData2" />
                </div>
                <div class="b_reply_footer">
                    <textarea class="b_reply_footer_input" v-model=state.reply rows="4" cols="120"></textarea>
                    <q-btn class="b_reply_footer_btn" outline @click="handleReply" icon="edit" size="20px" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity';
import { computed, onMounted } from '@vue/runtime-core';
import axios from 'axios';
import { useRoute, useRouter } from 'vue-router';
import { useStore } from 'vuex';
export default {
    setup () {
        const router = useRouter();
        const route = useRoute();
        const store = useStore();
        const moment = require('moment');
        
        const state = reactive({
            board : "<p>The Content</p>",
            no : Number(route.query.no),
            title: "",
            content : "",
            boardreply : "",
            page : 1,
            total : 1,
            reply : "",
            token : sessionStorage.getItem("TOKEN"),
            logged : computed(() => store.getters.getLogged),
            nickname : "",
            id : "",
            upreply : "",
            upreplyno : 0,
            result : "",
            nextno : 0,
            prevno : 0,
            reptotal : 0,
        });

        const handleData = async () =>{
            const url = `/COCO/api/board/selectone?no=`+state.no;
            const headers = {"Content-Type" : "application/json"};

            const { data } = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200){
                state.title = data.result.title;
                state.content = data.result.content;
                state.nickname = data.result.nickname;
                state.nextno = data.result.nextno;
                state.prevno = data.result.prevno;
                state.result = data.result;
                document.getElementById("board").innerHTML = state.content;
            }
        }

        const handleData2 = async () =>{
            const url = `/COCO/api/boardreply/selectlist.json`;
            const headers = {"Content-Type" : "application/json"};
            const body = {
                "boardno" : state.no,
                "page" : state.page
            };

            const { data } = await axios.post(url, body, {headers});
            console.log(data);
            if(data.status === 200){
                state.boardreply = data.result.boardreply;
                state.total = Math.ceil(data.result.total/5);
                state.reptotal = data.result.total;
            } 
        }

        const handleData3 = async() => {
        const url = `/COCO/api/member/selectone.json`;
        const headers = {
            "Content-Type":"application/json",
            "TOKEN" : state.token
        };
            const { data } = await axios.get(url, {headers});
            if(data.status === 200) {
                console.log(data);
                state.id = data.result.id;
            }
        };

        const handlelist = () => {
            router.push({path:'/community'});  
        }

        const handleupdate = () => {
            router.push({path:'/boardupdate', query:{no:state.no}});
        }

        const handledelete = async () => {
            if(confirm('삭제하시겠습니까?')){
                const url = `/COCO/api/board/delete.json`;
                const headers = {
                    "Content-Type":"application/json",
                    "TOKEN":sessionStorage.getItem("TOKEN")
                };
                const body = {
                    "no" : state.no,
                };

                const { data } = await axios.post(url, body, {headers});
                console.log(data);
                if(data.status === 200){
                    router.push({path:'/community'});  
                } 
                else{
                    handleData();
                }
            }
        }

        const handleReply = async () => {
            if(state.logged == false){
                alert('로그인을 해주세요');
                router.push('/login');
            }
            else {
                const url = `/COCO/api/boardreply/insert.json`;
                const headers = {
                    "Content-Type":"application/json",
                    "TOKEN":sessionStorage.getItem("TOKEN")
                };
                const body = {
                    "boardno" : state.no,
                    "content" : state.reply,
                };

                const { data } = await axios.post(url, body, {headers});
                console.log(data);
                if(data.status === 200){
                    state.reply = "";
                    handleData2();
                }
            }
        }

        const handlelike = async () => {
            if(state.logged == false){
                alert('로그인을 해주세요');
                router.push('/login');
            }
            else {
                const url = `/COCO/api/boardlike/insert.json`;
                const headers = {
                    "Content-Type":"application/json",
                    "TOKEN":sessionStorage.getItem("TOKEN")
                };
                const body = {
                    "boardno" : state.no,
                };
                const { data } = await axios.post(url, body, {headers});
                console.log(data);
                if(data.status === 200){
                    handleData();
                }
                else {
                    alert('이미 추천을 눌렀습니다');
                } 
            }
        }

        const handleupdatereply = (val) => {
            state.upreply = val.content;
            state.upreplyno = val.no;
        }

        const cancelrepupdate = () => {
            state.upreplyno = 0;
        }

        const handlerepupdate = async() => {
            if(confirm('수정하시겠습니까?')){
                const url = `/COCO/api/boardreply/update.json`;
                const headers = {
                    "Content-Type":"application/json",
                    "TOKEN":sessionStorage.getItem("TOKEN")
                };
                const body = {
                    "no" : state.upreplyno,
                    "content" : state.upreply,
                };

                const { data } = await axios.put(url, body, {headers});
                console.log(data);
                if(data.status === 200){
                    handleData2();
                    state.upreplyno = 0;
                } 
            }
        }

        const handledeletereply = async(val) => {
            if(confirm('삭제하시겠습니까?')){
                const url = `/COCO/api/boardreply/delete.json`;
                const headers = {
                    "Content-Type":"application/json",
                    "TOKEN":sessionStorage.getItem("TOKEN")
                };
                const body = {
                    "no" : val.no,
                    "id" : val.nickname,
                };

                const { data } = await axios.post(url, body, {headers});
                console.log(data);
                if(data.status === 200){
                    handleData2();
                } 
            }
        }

        const handleNext = () => {
            if(state.nextno != 0){
                state.no = state.nextno;
                router.push({path:'/boardone', query:{no:state.no}});
                handleData();
                handleData2();
                if(state.logged){
                    handleData3();
                }
            }
            else{
                alert('다음글이 없습니다.');
                return false;
            }
        }

        const handlePrev = () => {
            if(state.prevno != 0){
                state.no = state.prevno;
                router.push({path:'/boardone', query:{no:state.prevno}});
                handleData();
                handleData2();
                if(state.logged){
                    handleData3();
                }
            }
            else{
                alert('이전글이 없습니다.');
                return false;
            }
        }

        onMounted(()=>{
            handleData();
            handleData2();
            if(state.logged){
                handleData3();
            }
        })

        return {state, handleData2, handleReply, handlelist, handleupdate, handlelike, handleupdatereply, handlerepupdate, moment, cancelrepupdate, handledeletereply,
            handledelete, handleNext, handlePrev}
    }
}
</script>

<style lang="scss" scoped>
    .b_wrap {
        padding: 50px;
        display: flex;
        justify-content: center;
    }
    .b_article {
        width: 1000px;
    }
    .b_article_header {
        padding: 15px;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid;
        border-radius: 0.5em;
        background-color: #74b59d;
        color: white;
        font-size: 15px;
        font-weight: 100;
    }
    .b_article_header_col {
        width: 150px;
    }
    .b_article_header_right {
        width: 150px;
        display: flex;
        justify-content: right;
    }

    .b_article_title {
        text-align: center;
    }

    .b_article_content {
        word-break:break-all;
    }
    .b_article_content_footer {
        display: flex;
        justify-content: center;
        margin-top: 50px;
    }
    .b_article_side {
        display: flex;
        justify-content: flex-end;
        color: #BEAAA9;
    }
    .b_article_content_footer_btn {
        width: 100px;
        height: 60px;
        color: white;
        background-color: #74b59d;
    }
    .b_article_footer {
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid;
        border-radius: 0.5em;
        background-color: #74b59d;
        color: white;
        margin: 5px 0 20px 0;
        padding: 5px;
    }
    .b_reply_title {
        display: flex;
        column-gap: 2px;
        color: #74b59d;
        padding-bottom: 10px;
        margin-bottom: 20px;
        border-bottom: 1px solid #74b59d;
        font-weight: 600;
        font-size: 17px;
    }
    .b_reply_icon {
        color:#74b59d;
    }
    .b_reply_header {
        display: flex;
        justify-content: space-between;
    }
    
    .b_reply_update {
        display: flex;
        justify-content: space-between;
        border: 1px solid #9286B5;
        border-radius: 0.5em;
        padding: 20px;

    }
    .b_reply_update_input {
        border: 1px solid #9286B5;
        border-radius: 0.5em;
    }

    .b_reply_update_btn {
        display: flex;
        column-gap: 0.5px;
    }

    .b_reply_update_btn_update {
        width: 80px;
        color: #9286B5;
    }

    .b_reply_update_btn_del {
        width: 80px;
        color: #B57162;
    }

    .b_reply_header_icon_edit {
        width: 30px;
        color: #9286B5;
    }
    .b_reply_header_icon_del {
        width: 30px;
        color: #B57162;
    }
    .b_reply_article {
        width: 1000px;
        border-bottom: 1px solid whitesmoke;
        padding-bottom: 10px;
        margin-bottom: 5px;
        word-break:break-all;
    }

    
    .b_reply_footer {
        padding: 20px;
        display: flex;
        justify-content: space-between;
        border: 1px solid #74b59d;
        border-radius: 0.5em;
    }

    .b_reply_footer_input {
        border: 1px solid #74b59d;
        border-radius: 0.5em;
    }

    .b_reply_footer_btn {
        width: 80px;
        color: #74b59d;
    }
</style>