<template>
    <div>
        <!-- {{state.teachercode}}
        {{state.teacherid}} -->
    <div class="head-line">
        <div class="main-img">
            <q-img :src="`/COCO/api/cours/image?no=${state.imgno}`" class="rounded-borders" :ratio="4/3" width="400px" style="width:400px;height:300px;margin-left:10%; margin-top:100px;" />
        </div>

        <div class="head-info">
            <div>
                <h5>{{state.cours.title}}</h5>
            </div>

            <div>
                <q-rating v-model="state.ratingInt" size="1.8em" no-dimming icon="star_border" icon-selected="star" readonly  /> 
                <span v-if="state.ratingLength > 0" class="text-grey-9">({{state.ratingFloat}})</span>
                <span v-if="state.ratingLength > 0">&nbsp; {{state.ratingLength}}개의 리뷰</span>
            </div>

            <div>
                <!-- <span class="teacher-name text-black" @click="handleTeacher(state.teachercode)">{{state.teachername}}</span> -->
                <q-btn-dropdown class="tchr-btn" no-icon-animation :ripple="false"  dense color="grey-7" flat :label="state.teachername">
                    <q-list>
    
                        <q-item clickable dense v-close-popup @click="handleTeacher(state.teachercode)">
                            <q-item-section>
                                <q-item-label>강사정보</q-item-label>
                            </q-item-section>
                        </q-item>
    
                        <q-item clickable dense v-close-popup @click="handleChat(state.teacherid)">
                            <q-item-section>
                                <q-item-label>메시지 보내기</q-item-label>
                            </q-item-section>
                        </q-item>
    
                    </q-list>
                </q-btn-dropdown>
            </div>

            <div v-for="tmp in state.category" :key="tmp" style="display:inline-block;">
                <q-chip icon="music_note" clickable class="bg-white" square dense outline color="secondary" text-color="white" @click="handleCategorySearch(tmp.name)">{{tmp.name}}</q-chip>
            </div>
        </div>
    </div>

    <div class="row">
        <!-- {{state}} -->
        <div class="col-1"></div>

        <!-- 코스 메인정보 -->
        <div class="col-6 intro">    

            <!-- tab 만드는 중 -->
            <q-tabs  v-model="tab" dense align="left" class=" text-secondary" :breakpoint="0">
                
                <q-tab name="intro" label="강좌소개" @click="menu = 1" />
                <q-tab name="curriculum" label="커리큘럼" @click="menu = 4" />
                <q-tab name="review" label="리뷰" @click="menu = 2" />
                <q-tab name="comment" label="게시판" @click="menu = 3" />
 
            </q-tabs>

            <menu-1 v-if="state.menu === 1" :intro="state.intro"></menu-1>
            <div v-if="state.menu == 1">
                <div id="intro"></div>
            </div>

            <menu-4 v-if="menu === 4" :data="state.data" :cours="state.cours" :chapterList="state.chapterList" ></menu-4>
            
            <menu-2 v-if="menu === 2" title="리뷰" :token = "state.token" :cours="state.cours" :orderStatus="state.orderStatus"></menu-2>
            
            <menu-3 v-if="menu === 3" title="게시판" :cours = "state.cours" ></menu-3>
            
            
        </div>




        <!-- 나중에 다시 바꾸려면 div만 menu-1로 바꾸면 됨
        <div v-if="menu === 0" title="강좌소개" :data="state.data"
                :chapterList="state.chapterList" :cours="state.cours"
            ></div> -->

        <!-- 정보&액션 창 -->
        <div class="col-4 sub" style="padding: 10px;">
            <div>

                <q-card class="order-card">
                    <div v-if="state.orderStatus === 0">
                        <q-card-section>
                            <!-- <div class="text-subtitle2">{{state.price}}원</div> -->
                            <div class="text-h6">
                                가격 : {{state.price}}원 
                                <!-- <span class="text-subtitle2" style="color:grey">5개월 할부시</span> -->
                            </div>
                        </q-card-section>
                    </div>

                    <div v-if="state.orderStatus === 1">
                        <q-card-section>
                            <div class="text-h6">구매완료</div>
                                
                        </q-card-section>

                    </div>
                        
                    <q-separator style="width:90%; margin:auto" />

                    <q-card-actions vertical style="width:107%">
                        <!-- 구매 리뷰 버튼 -->
                        <div v-if="state.orderStatus === 0">
                            <div v-if="state.logged">
                                <q-btn class="btn-box" color="secondary" label="구매하기" @click="modalOrder = true" />
                            </div>

                            <div v-if="!state.logged">
                                <q-btn class="btn-box" color="secondary" label="구매하기" @click="handleOrderLogin" />
                            </div>
                        </div>
                        
                        <div v-if="state.orderStatus === 1">

                            <q-btn color="secondary" label="레슨하러 가기" @click="handleStudy" />

                            <div v-if="state.reviewStatus !== 1">
                                <q-btn class="btn-box" color="secondary" outline label="리뷰쓰기" @click="modalReview = true" />
                            </div>
                        </div>

                        <div v-if="!state.wish">
                            <div v-if="state.orderStatus !== 1">
                                <q-btn class="btn-box" outline color="secondary" @click="handleInsertWish(state.coursno)" icon="favorite_border" label="관심강좌 추가" />
                            </div>
                        </div>
                        <div v-if="state.wish">
                            <q-btn class="btn-box" outline color="secondary" @click="handleDeleteWish(state.wishno)" icon="favorite" label="관심강좌 취소" />
                        </div>

                    </q-card-actions>

                    <div class="order-info bg-grey-1">
                        <q-card-section>
                                <p class="info-text">&bull; 강사 : <span class="teacher-name text-primary" @click="handleTeacher(state.teachercode)">{{state.teachername}}</span> </p>
                                <p class="info-text">&bull; 분류 : {{state.cours.instrument}}</p>
                                <p class="info-text">&bull; 수강기한 : {{state.cours.period}}</p>
                                <p class="info-text">&bull; 난이도 : {{state.cours.difficult}}</p>
                        </q-card-section>
                    </div>

                </q-card>
                
            </div>


            <div class="col-1"></div>


            <!-- 구매 modal -->
            <q-dialog persistent v-model="modalOrder">
                <q-card style="width:360px;">
                    <q-card-section class="row">
                        <q-item>

                            <q-item-section style="max-width:90px;">
                                <q-avatar rounded size="72px">
                                    <q-img :src="`/COCO/api/cours/image?no=${state.imgno}`" />
                                </q-avatar>
                            </q-item-section>
                                
                            <q-item-section style="width:200px;">
                                <q-item-label class="text-bold">{{state.cours.title}}</q-item-label>
                                    
                                <q-item-label>
                                    {{state.teachername}}
                                </q-item-label>

                                <q-item-label>
                                    {{state.price}}원
                                </q-item-label>
                            </q-item-section>

                        </q-item>
                    </q-card-section>

                    <q-separator />

                    <q-card-actions>
                        <span style="margin-left:20px;">{{pay}}</span>
                        <q-space />
                        <q-btn color="grey" label="결제수단 선택" flat dense 
                            :icon="expanded ? 'keyboard_arrow_up' : 'keyboard_arrow_down'"
                            @click="expanded = !expanded" />
                    </q-card-actions>

                    <!-- 결제방식 expanded -->
                    <q-slide-transition>
                        <div v-show="expanded">
                        <q-card-section class="text-subitle2">
                            <q-radio v-model="pay" val="신용카드" @click="expanded = !expanded" label="신용카드" color="secondary" /><br />
                            <q-radio v-model="pay" val="휴대폰결제" @click="expanded = !expanded" label="휴대폰결제" color="secondary" /><br />
                            <q-radio v-model="pay" val="카카오페이" @click="expanded = !expanded" label="카카오페이" color="secondary" /><br />
                            <q-radio v-model="pay" val="계좌이체" @click="expanded = !expanded" label="계좌이체" color="secondary" />
                        </q-card-section>
                        </div>
                    </q-slide-transition>

                    <q-separator />
                    <q-form @submit="handleInsertOrder(state.coursno)">
                        <q-card-actions align="right">
                            <q-btn flat color="primary" label="결제" type="submit" />
                            <q-btn v-close-popup flat color="grey-7" label="취소" />
                        </q-card-actions>
                    </q-form>


                </q-card>
            </q-dialog>
        </div>

            <!-- 리뷰 modal -->
            <q-dialog persistent v-model="modalReview">
                <q-card style="width:360px;">
                    <q-card-section class="row">
                        <q-item>

                            <q-item-section style="max-width:90px;">
                                <q-avatar rounded size="72px">
                                    <q-img :src="`/COCO/api/cours/image?no=${state.imgno}`" />
                                </q-avatar>
                            </q-item-section>
                                
                            <q-item-section style="width:200px;">
                                <q-item-label class="text-bold">{{state.cours.title}}</q-item-label>
                                    
                                <q-item-label>
                                    {{state.teachername}}
                                </q-item-label>

                                <q-item-label>
                                    {{state.price}}원
                                </q-item-label>

                                <br />
                                <q-rating v-model="state.rating" max="5" size="2em" no-dimming icon="star_border" icon-selected="star" />

                            </q-item-section>

                        </q-item>
                    </q-card-section>

                    <q-separator />

                        
                    <!-- <q-card-section> -->
                        <q-input borderless autogrow type="textarea" color="secondary" v-model="state.content" 
                            style="padding:0 20px 20px"  placeholder="리뷰 내용을 남겨주세요." counter maxlength="200" />
                    <!-- </q-card-section> -->
                    <q-separator />

                    <q-card-actions align="right">
                        <q-btn flat color="primary" @click="handleInsertReview(state.coursno)" label="등록" />
                        <q-btn v-close-popup flat color="grey-7" label="취소" />
                    </q-card-actions>
                    
                </q-card>
            </q-dialog>        

    </div>
    </div>
</template>

<script>
import { reactive, ref, toRefs } from '@vue/reactivity'
import { useRoute, useRouter } from 'vue-router'
import axios from 'axios';
import { computed, onMounted } from '@vue/runtime-core';
import { useStore } from 'vuex';
import Menu1 from './CoursIntroPage.vue';
import Menu2 from './CoursReviewPage.vue';
import Menu3 from './CoursCommentPage.vue';
import Menu4 from './CoursCurriculumPage.vue';
export default {
    components : {
        Menu1,
        Menu2,
        Menu3,
        Menu4
    },
    setup() {
        const store = useStore();
        const route = useRoute();
        const router = useRouter();
        const state = reactive({
            logged : computed(() => store.getters.getLogged),
            token : sessionStorage.getItem("TOKEN"),
            coursno : route.query.coursno,
            menu : 1,
            data : [], // 이거 필요해서 넣은거임 지우면 안됨
            cours : [],
            category : [],
            chapterList : [],
            imgno : 0,
            img : require('@/assets/images/no_image.png'),
            price : '',
            priceAvg : '',
            wish : false,
            wishno : null,
            teachername : '',
            teachercode : null,
            teacherid : '',
            orderStatus : 0,
            rating : 0,
            ratingInt : 0,
            ratingFloat : 0,
            ratingLength : 0,
            content: '',
            modalReview : false,
            modalOrder : false,
            expanded : false,
            pay : '결제수단을 선택해 주세요.',
            reviewStatus : 0,
            intro : ''
            
        })

        state.price.toLocaleString();

        const handleData = async() => {
            const url = `/COCO/api/cours/selectone.json?coursno=${state.coursno}`;
            const headers = {"Content-Type":"application/json"}
            
            const { data } = await axios.get(url, {headers:headers});
            console.log('강좌조회',data)
            state.data = data
            state.cours = data.cours
            state.category = data.categorylist
            state.imgno = data.coursimage
            state.chapterList = data.chapterlist;
            state.price = data.cours.price.toLocaleString('kr-KR');
            state.priceAvg = (data.cours.price/5).toLocaleString('kr-KR');
            state.teachername = data.teachername
            state.teachercode = data.teachercode
            state.intro = data.cours.intro
            handleDataTeacher();
            handleDocument();
        }

        const handleCategorySearch = (obj) => {
            store.commit('setCategorySearch', obj)
            router.push({path:'/cours'})
        }

        const handleDocument = () => {
            document.getElementById("intro").innerHTML = state.intro
        }

        const handleStudy = () => {
            router.push({path:'/study', query:{coursno : state.coursno}})
        }

        const handleCheckOrder = async() =>{
            if(state.logged) {
                const url = `/COCO/api/order/search.json?coursno=${state.coursno}`;
                const headers = {
                    "Content-Type":"application/json",
                    "token" : state.token
                };
                const { data } = await axios.get(url, {headers});
                console.log('구매여부',data)
                if(data.status === 200) {
                    state.orderStatus = data.orderstatus
                }
            }
        }

        const handleRatingData = async() => {
            const url = `/COCO/api/solda/ratingavg.json?courscode=${state.coursno}`
            const headers = {
                "Content-Type":"application/json",
            };
            const { data } = await axios.get(url, {headers});
            console.log('리뷰 점수 => ', data)
            if(data.status === 200) {
                state.ratingLength = data.count;
                state.ratingInt = Math.round(data.average);
                state.ratingFloat = Math.round(data.average * 10) / 10;
            }
        }

        const handleInsertWish = async(obj) => {
            if(state.token !== null) {
                const url = `/COCO/api/wish/add.json?coursno=${obj}`;
                const headers = {
                    "Content-Type":"application/json",
                    "TOKEN" : state.token
                };
    
                const {data} = await axios.post(url, {}, {headers});
                console.log('좋아요 활성',data);
                if(data.status === 200) {
                    handleData();
                    handleCheckWish();
                }
            }
            else {
                if(confirm('로그인이 필요합니다. 로그인 하시겠습니까?')) {
                    store.commit('setLoginUrl', true)
                    router.push({path:'/login'})
                }
            }
        }

        const handleDeleteWish = async(obj) => {
            const url = `/COCO/api/wish/delete.json?wishno=${obj}`;
            const headers = {
                "Content-Type":"application/json"
            }
            const { data } = await axios.post(url, {}, {headers});
            console.log('좋아요 비활성',data);
            if(data.status === 200) {
                handleData();
                handleCheckWish();
            }
        }

        const handleCheckWish = async() => {
            if(state.token !== null) {
                const url = `/COCO/api/wish/check.json?coursno=${state.coursno}`;
                const headers = {
                    "Content-Type":"application/json",
                    "token" : state.token
                };
                const { data } = await axios.get(url, {headers});
                console.log('좋아요여부',data);
                if(data.status === 200) {
                    state.wishno = data.wishcheck;
                    state.wish = true;
                }
                else {
                    state.wish = false;
                }
            }
        }

        const handleCheckReview = async() => {
            if(state.token !== null) {
                const url = `/COCO/api/review/checkreview.json?courscode=${state.coursno}`;
                const headers = {
                    "Content-Type":"application/json",
                        "TOKEN" : state.token
                };
                const { data } = await axios.get(url, {headers});
                console.log('리뷰체크 => ', data)
                if(data.status === 200) {
                    state.reviewStatus = 1;
                }
                else if(data.status === 0) {
                    state.reviewStatus = 0;
                }
            }
        }

        const handleTeacher = (obj) => {
            router.push({path:'/tchrselect', query : {teacherno : obj}})
        }

        const handleChat = (id) => {
            router.push({path:'/chatcontent', query : {receiver : id}});
        };

        const handleInsertOrder = async() => {
            if(state.token !== null) {
                if(state.pay === '결제수단을 선택해 주세요.') {
                alert('결제수단을 선택해주세요.');
                return false;
                }

                const url = `/COCO/api/order/insert.json?coursno=${state.coursno}`;
                const headers = {
                    "Content-Type" : "application/json",
                    "TOKEN" : state.token
                };
                const body = {
                    status : 1
                }
                const { data } = await axios.post(url, body, {headers})
                console.log('결제하기', data);

                if (data.status === 200 && state.pay === '카카오페이') {
                    router.push({path:'/paytest', query:{ orderno : data.orderno}})
                }

                else if(data.status === 200 && state.pay !== '카카오페이' ) {
                    handleDeleteWish(state.coursno);
                    state.wish = false;
                    handleCheckOrder();
                    state.modalOrder = false
                    alert('결제가 완료 되었습니다.')
                }
            }
            else {
                if(confirm('로그인이 필요합니다. 로그인 하시겠습니까?')) {
                    store.commit('setLoginUrl', true)
                    router.push({path:'/login'})
                }
            }
        }

        const handleOrderLogin = () => {
            if(confirm('로그인이 필요합니다. 로그인 하시겠습니까?')) {
                store.commit('setLoginUrl', true)
                router.push({path:'/login'})
            }
        }

        const handleInsertReview = async(obj) => {
            if(state.rating === 0) {
                alert('별점을 입력하세요.');
                return false;
            }
            if(state.content === '') {
                alert('리뷰 내용을 입력하세요.');
                return false;
            }
                const url = `/COCO/api/review/write.json?courscode=${obj}`;
                const headers = {
                    "Content-Type":"application/json",
                    "token" : state.token
                };
                const body = {
                    rating : state.rating,
                    content : state.content
                }
                
                const { data } = await axios.post(url, body, {headers});
                console.log(data)
                if(data.status === 200) {
                    handleData();
                    handleCheckReview();
                    handleRatingData();
                    alert('리뷰가 등록 되었습니다.')
                    state.modalReview = false;
                }
        }
                
        const handleDataTeacher = async () => {
            const url = `/COCO/api/teacher/selectteacher.json?teachercode=${state.teachercode}`;
            const headers = {"Content-Type":"application/json"};
            const {data} = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200){
                state.teacherid = data.teacher.id;
            }
        };

        onMounted(() => {
            handleData();
            handleCheckWish();
            handleCheckOrder();
            handleRatingData();
            handleCheckReview();
        })

        return {
            state,
            handleInsertWish,
            handleDeleteWish,
            handleTeacher,
            handleInsertOrder,
            tab: ref('intro'),
            ...toRefs(state),
            handleInsertReview,
            handleChat,
            handleOrderLogin,
            handleCategorySearch,
            fab: ref(true),
            handleStudy
        }
    }
}
</script>

<style lang="css" scoped src="@/assets/css/CoursSelectOne.css">

</style>