<template>
  <div align="center"  class="back-back"></div>

  <!-- <q-dialog v-model="loginModal" persistent> -->
  <div class="login_box">
    <q-card class="login_card" style="opacity:0.9">
      <q-card-section horizontal>
        <q-img src="@/assets/images/bg/tropical_guitar_small.jpg"
          width="50%" />

          <div style="width:360px">

            <q-card-section align="right">
              <q-img src="@/assets/images/logo/4Dlogo.png" 
                width="80px" />
            </q-card-section>

            <q-card-section align="center">
              <div class="text-overline"
                style=" margin-top: 10px; font-size:48px; color:#4d9056;">
                  LOGIN
                </div>
            </q-card-section>

            <q-form @submit="handleLogin">
              <q-card-section align="center">
                <q-input color="secondary" class="login_card_item" autofocus
                  v-model="state.id" label="아이디" />

                <q-input color="secondary" :type="isPwd ? 'password' : 'text'"
                    class="login_card_item" v-model="state.pw" label="비밀번호">
                  <template v-slot:append>
                    <q-icon
                      :name="isPwd ? 'visibility_off' : 'visibility'"
                      class="cursor-pointer" @click="isPwd = !isPwd" />
                  </template>
                </q-input>
              </q-card-section>

              <q-card-actions style="margin-top:10px;" align="center">
                <q-btn color="primary" type="submit" 
                  class="login_card_item" label="로그인" />
              </q-card-actions>
            </q-form>

            <div style="margin:-15px;">
              <q-card-section align="center">
                <router-link class="text-caption" 
                    style="text-decoration:none;color:dimgrey;" 
                    onmouseover="this.style.color='black';" onmouseout="this.style.color='dimgrey';"
                    to="/login" @click="handleCheck"> 아이디 &middot; 비밀번호 찾기 </router-link> |

                <router-link class="text-caption"
                    style="text-decoration:none;color:dimgrey;" 
                    onmouseover="this.style.color='black';" onmouseout="this.style.color='dimgrey';"
                    to="/join"> 회원가입 </router-link>
              </q-card-section>
            </div>
            
              <q-card-section align="center">
                <div class="hr-sect">OR</div>
              </q-card-section>

              <div align="center">
                  <div id="naver_id_login"></div>
              </div>

              
              
            <!-- <div style="margin:-35px;">
              <q-card-section align="center">
                <q-img @click=handleQuit() class="api_icon" src="@/assets/images/icon/google.png" />
                <q-img class="api_icon" src="@/assets/images/icon/naver.png" />
                <q-img class="api_icon" src="@/assets/images/icon/cacao.png" />
              </q-card-section>
            </div>

            <div style="margin-top:20px;">
              <q-card-section align="center">
                <q-btn @click="handleTeacherVue()" flat style="color:red" label="강사 로그인" />
              </q-card-section>
            </div> -->

          </div>
      </q-card-section>
  </q-card>

  <!-- </q-dialog> -->

  </div>
</template>

<script>
import { reactive, ref } from '@vue/reactivity';
import { useRouter } from 'vue-router';
import axios from 'axios';
import { useStore } from 'vuex';
import { computed } from '@vue/runtime-core';
export default {
  mounted(){
    const naver_id_login = new window.naver_id_login("YGknk7lCNc_ce4kKjir0", "http://3.35.91.60:8080/COCO/naverlogin");
    const state = naver_id_login.getUniqState();
    naver_id_login.setState(state);
    // naver_id_login.setPopup(); // popup 설정을 위한 코드
    naver_id_login.init_naver_id_login();
  },
  setup() {
      const router = useRouter();
      const store = useStore();
      const state = reactive({
          id    : '',
          pw    : '',
          loginUrl : computed(() => store.getters.getLoginUrl)
      });

      const loginModal = ref();

      const handleLogin = async () => {
          const url = `/COCO/api/member/login.json`;
          const headers = {"Content-Type":"application/json"};
          const body = {
              id : state.id,
              pw : state.pw
          };

          const {data} = await axios.post(url, body, {headers});
          if(data.status === 200) {
              alert('로그인 되었습니다.');
              sessionStorage.setItem("TOKEN", data.result);
              store.commit('setLogged', true);
              store.commit('setAppToken', data.result);
              loginModal.value = false;
              if(state.loginUrl === true) {
                router.go(-1);
                store.commit('setLoginUrl', false)
              }
              else{
                router.push({path:'/'});
              }
              store.dispatch('handleMemberData');
          }
          else {
            alert('아이디 혹은 비밀번호가 일치하지 않습니다.')
          }
      };

      const handleCheck = () => {
        router.push({path:'/check'});
      }

      const handleQuit = () => {
        router.go(-1);
      }

      return {
        state,
        handleLogin,
        handleQuit,
        handleCheck,
        isPwd:ref(true),
        loginModal:ref(true),
      }
  }
}
</script>

<style lang='css' scoped src='@/assets/css/Sign.css'>

</style>