<template>
    <div>
        <h5>오더</h5>
        {{state.cours}}
        <button @click="handleInsertCours">결제</button>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import { useRoute, useRouter } from 'vue-router'
import axios from 'axios';
import { onMounted } from '@vue/runtime-core';
export default {
    setup() {
        const route = useRoute();
        const router = useRouter();
        const state = reactive({
            coursno : route.query.coursno,
            cours : [],
            wishList : [],
            token : sessionStorage.getItem("TOKEN")
        })

        const handleCoursData = async() => {
            const url = `/COCO/api/cours/selectone.json?coursno=${state.coursno}`;
            const headers = {"Content-Type":"application/json"}
            
            const { data } = await axios.get(url, {headers:headers});
            console.log(data)
            state.cours = data.cours
        }

        const handleWishData = async() => {
            const url = `/COCO/api/wish/selectwish.json`;
            const headers = {
                "Content-Type":"multipart/form-data",
                "TOKEN" : state.token
            };
            const { data } = await axios.get(url, {headers})
            console.log(data)
            if(data.status === 200) {
                state.wishList = data.wish
            }
        }
        
        const handleInsertCours = async() => {
            const url = `/COCO/api/order/insert.json?coursno=${state.coursno}`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            };
            const body = {
                status : 1
            }
            const { data } = await axios.post(url, body, {headers})
            console.log(data);
            if(data.status === 200) {
                alert('결제가 완료 되었습니다.')
                router.push({path: '/cours'})
            }
        }

        onMounted(() => {
            handleCoursData();
            handleWishData();
        })

        return {
            state,
            handleInsertCours
        }
    }
}
</script>

