<template>
    <div class="q-pa-md row">
        <div class="col-3"></div>

        <div class="col-6">
            <br />
            <div class="row">
                <q-input type="text" borderless color="secondary" v-model="state.title" :maxlength="30"
                    placeholder="제목을 입력하세요" input-class="text-h6" style="width:85%" />
                    <q-space />
                <q-checkbox size="xs" left-label color="secondary" v-model="state.secret" :true-value=1 :false-value=0>
                    <q-icon name="lock" />
                </q-checkbox>
            </div>

            <q-card>
                <q-input borderless autogrow type="textarea" color="secondary"
                    v-model="state.content" style="height: 500px;margin: 0 15px;" counter maxlength="500" />
            </q-card>

            <div class="row" style="margin-top:10px;">
                <div class="text-grey text-body2">{{state.coursTitle}}</div>
                <q-space />
                <q-btn @click="handleCancer" label="취소" style="margin-right:10px;" />
                <q-btn color="secondary" @click="handleInsert" label="등록" />
            </div>
        </div>

        <div class="col-3"></div>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import { useRoute, useRouter } from 'vue-router'
import axios from 'axios';
import { onMounted } from '@vue/runtime-core';
export default {
    setup() {
        const route = useRoute();
        const router = useRouter();
        const state = reactive({
            token : sessionStorage.getItem("TOKEN"),
            coursno : route.query.coursno,
            title : '',
            content : '',
            secret : 0,
            coursTitle : ''
        })

        const handleCoursData = async() => {
            const url = `/COCO/api/cours/selectone.json?coursno=${state.coursno}`;
            const headers = {"Content-Type":"application/json"}
            
            const { data } = await axios.get(url, {headers:headers});
            state.coursTitle = data.cours.title
        }

        const handleInsert = async() => {
            if(state.title === '') {
                alert('제목을 입력하세요');
                return false;
            }
            if(state.content === '') {
                alert('내용을 입력하세요');
                return false;
            }
            const url = `/COCO/api/comment/insert.json?coursno=${state.coursno}`;
            const headers = {
                "Content-Type":"application/json",
                "TOKEN" : state.token
            };
            const body = {
                title : state.title,
                content : state.content,
                secret : state.secret
            };

            const { data } = await axios.post(url, body, {headers});
            console.log(data)
            if(data.status === 200) {
                alert('등록되었습니다.');
                router.push({path:'/coursone', query : {coursno : state.coursno}})
            }
        }

        const handleCancer = () => {
            router.go(-1);
        }

        onMounted(() => {
            handleCoursData();
        })

        return {
            state,
            handleInsert,
            handleCancer
        }
    }
}
</script>

