<template>

<div class="head-line">
        <div class="main-img">
            <q-img :src="`/COCO/api/cours/image?no=${state.imgno}`" class="rounded-borders" :ratio="4/3" width="400px" style="width:400px;height:300px;margin-left:10%; margin-top:100px;" />
        </div>

        <div class="head-info">
            <div>
                <h5>{{state.cours.title}}</h5>
            </div>

            <div>
                <q-rating v-model="state.ratingInt" size="1.8em" no-dimming icon="star_border" icon-selected="star" readonly  /> 
                <span v-if="state.ratingLength > 0" class="text-grey-9">({{state.ratingFloat}})</span>
                <span v-if="state.ratingLength > 0">&nbsp; {{state.ratingLength}}개의 리뷰</span>
            </div>

            <div>
                <!-- <span class="teacher-name text-black" @click="handleTeacher(state.teachercode)">{{state.teachername}}</span> -->
                <q-btn-dropdown class="tchr-btn" no-icon-animation :ripple="false"  dense color="grey-7" flat :label="state.teachername">
                    <q-list>
    
                        <q-item clickable dense v-close-popup @click="handleTeacher(state.teachercode)">
                            <q-item-section>
                                <q-item-label>강사정보</q-item-label>
                            </q-item-section>
                        </q-item>
    
                        <q-item clickable dense v-close-popup @click="handleChat(state.teacherid)">
                            <q-item-section>
                                <q-item-label>메시지 보내기</q-item-label>
                            </q-item-section>
                        </q-item>
    
                    </q-list>
                </q-btn-dropdown>
            </div>

            <div v-for="tmp in state.category" :key="tmp" style="display:inline-block;">
                <q-chip icon="music_note" clickable class="bg-white" square dense outline
                    color="secondary" text-color="white" :ripple="false">{{tmp.name}}</q-chip>
            </div>
        </div>
    </div>

    <div class="row">
        <div class="col-2"></div>
        <div class="col-8">
            <br />
            <q-tabs v-model="tab" dense align="left" class=" text-secondary" :breakpoint="0">              
                <q-tab name="dashboard" label="대시보드" @click="menu = 1" />
                <q-tab name="intro" label="강좌소개" @click="menu = 2" />
                <q-tab name="comment" label="게시판" @click="menu = 3" />
            </q-tabs>

            <menu-1 v-if="menu === 1" :data="state.data" :chapterList="state.chapterList"
                :cours="state.cours" @sendData="handleSelectOneLecture">
            
            </menu-1>

            <menu-2 v-if="menu === 2" :intro="state.cours.intro">

            </menu-2>

            <menu-3 v-if="menu === 3" :cours = "state.cours">

            </menu-3>


        </div>        
        <div class="col-2"></div>




        <q-dialog persistent v-model="modalLecture">
            <q-card style="min-width:80%; height:80%; padding:20px;">

                <q-btn fab icon="close" square color="grey-7" v-close-popup class="fab" flat />

                <q-card-section>
                    <div class="text-h6">{{state.lectureData.title}}</div>
                    <!-- <q-space />
                    <q-btn dense color="negative" label="닫기" v-close-popup /> -->
                </q-card-section>

                <q-card-section >
                    <!-- <video style="width:100%" controls>
                        <source :src="`/COCO/api/lecture/video?no=${state.videoNo}`" type="video/mp4" />
                        
                    </video> -->

                <!-- <q-card style="min-width:800px;min-height:600px;"> -->

                    <q-video v-if="state.videoNo !== 0" style="width:100%;height:50vh;" :src="`/COCO/api/lecture/video?no=${state.videoNo}`"/>

                    <div v-if="state.videoNo === 0" style="width:100%;max-width:100%;">
                        <div class="text-grey-7">해당 강의는 동영상이 없습니다.</div>
                    </div>

                <!-- </q-card> -->
                </q-card-section>


                <q-card-section style="width:100%;" class="scroll">
                    <div id="board"></div>
                </q-card-section>

                <!-- <q-separator />

                <q-card-actions align="right">
                    <q-btn dense color="negative" label="닫기" v-close-popup />
                </q-card-actions>
                 -->
            </q-card>

        </q-dialog>
    </div>
</template>

<script>
import { reactive, ref, toRefs } from '@vue/reactivity'
import { computed, onMounted } from '@vue/runtime-core';
import axios from 'axios';
import { useStore } from 'vuex';
import { useRoute, useRouter } from 'vue-router';
import Menu1 from '@/components/cours/CoursCurriculumPage.vue';
import Menu2 from '../cours/CoursIntroPage.vue';
import Menu3 from '@/components/cours/CoursCommentPage.vue';
export default {
    components : {
        Menu1,
        Menu2,
        Menu3
    },
    setup() {
        const store = useStore();
        const route = useRoute();
        const router = useRouter();
        const state = reactive({
            logged : computed(() => store.getters.getLogged),
            token : sessionStorage.getItem("TOKEN"),
            coursno : route.query.coursno,
            menu : 1,
            data : [], // 이거 필요해서 넣은거임 지우면 안됨
            cours : [],
            category : [],
            chapterList : [],
            imgno : 0,
            img : require('@/assets/images/no_image.png'),
            teachername : '',
            teachercode : null,
            orderCheck : false,
            modalLecture : false,
            lectureNo : 0,
            lectureData : [],
            videoNo : 0,
            content : '',
            ratingInt : 0,
            ratingFloat : 0,
            ratingLength : 0
        })

        const handleData = async() => {
            const url = `/COCO/api/cours/selectone.json?coursno=${state.coursno}`;
            const headers = {"Content-Type":"application/json"}
            
            const { data } = await axios.get(url, {headers:headers});
            console.log('강좌조회',data)
            state.data = data
            state.cours = data.cours
            state.category = data.categorylist
            state.imgno = data.coursimage
            state.chapterList = data.chapterlist;
            // state.price = data.cours.price.toLocaleString('kr-KR');
            // state.priceAvg = (data.cours.price/5).toLocaleString('kr-KR');
            state.teachername = data.teachername
            state.teachercode = data.teachercode
        }

        const handleRatingData = async() => {
            const url = `/COCO/api/solda/ratingavg.json?courscode=${state.coursno}`
            const headers = {
                "Content-Type":"application/json",
            };
            const { data } = await axios.get(url, {headers});
            console.log('리뷰 점수 => ', data)
            if(data.status === 200) {
                state.ratingLength = data.count;
                state.ratingInt = Math.round(data.average);
                state.ratingFloat = Math.round(data.average * 10) / 10;
            }
        }

        const handleOrderCheck = async() => {
            const url = `/COCO/api/order/search.json?coursno=${state.coursno}`;
            const headers = {
                "Content-Type":"application/json",
                "token" : state.token
            };
            const { data } = await axios.get(url, {headers});
            console.log(data)
            if(data.status === 200) {
                if(data.orderstatus === 1) {
                    console.log('구매확인')
                    state.orderCheck = true;
                }
            }
        }

        const handleSelectOneLecture = async(val) =>{
            if(state.orderCheck === true) {
                state.modalLecture = true
                console.log(val),
                state.lectureNo = val
                const url = `/COCO/api/lecture/selectone.json?lectureno=${val}`
                const headers = {
                    "Content-Type":"application/json"
                }
                const { data } = await axios.get(url, {headers});
                console.log(data)
                if(data.status === 200) {
                    state.lectureData = data.lecture;
                    state.videoNo = data.videono;
                    state.lectureNo = data.lecture.no
                    document.getElementById("board").innerHTML = data.lecture.content
                }
            }
        }

        const handleTeacher = (obj) => {
            router.push({path:'/tchrselect', query : {teacherno : obj}})
        }

        const handleChat = (id) => {
            router.push({path:'/chatcontent', query : {receiver : id}});
        };

        onMounted(() => {
            handleData();
            handleOrderCheck();
            handleRatingData();
        })

        return {
            state,
            tab : ref('dashboard'),
            ...toRefs(state),
            handleSelectOneLecture,
            handleTeacher,
            handleChat
        }
    }
}
</script>

<style lang="css" scoped>
    @import '@/assets/css/CoursSelectOne.css';
    .fab {
        position: absolute;
        z-index: 9999;
        top: 5px;
        left: 94%;
    }
</style>