<template>
    <div class="b_wrap">
        <div class="b_article">
            <q-btn-group push>
                <q-btn style="background: #74b59d; color: white" push label="전체" @click="handlecategory('')" />
                <q-btn style="background: #74b59d; color: white" push label="자유" @click="handlecategory('자유')" />
                <q-btn style="background: #74b59d; color: white" push label="질문" @click="handlecategory('질문')" />
                <q-btn style="background: #74b59d; color: white" push label="중고거래" @click="handlecategory('중고거래')" />
            </q-btn-group>
            <hr />
            <div class="b_article_header_btn">
                <q-btn v-if="state.like == 0" style="background: #F53569; color: white" push @click="handlelike" label="인기글" />
                <q-btn v-if="state.like == 1" style="background: #74b59d; color: white" push @click="handlelike" label="모든글" />
                <q-btn style="background: #74b59d; color: white" push @click="handlewrite" label="글쓰기" />
            </div>
            <div>
                <table class="b_table">
                    <tr class="b_table_header">
                        <th>번호</th>
                        <th>
                            <label v-if="state.category == '' ">카테고리</label>
                            <label v-if="state.category == '자유' " style="color: #BDFFE7;">자유</label>
                            <label v-if="state.category == '질문' " style="color: #BDFFE7;">질문</label>
                            <label v-if="state.category == '중고거래' " style="color: #BDFFE7;">중고거래</label>
                        </th>
                        <th>제목</th>
                        <th>글쓴이</th>
                        <th>작성일</th>
                        <th>
                            <label v-if="state.hit == '' " style="cursor: pointer; color: #BDFFE7;" @click="handlehit">조회수</label>
                            <label v-if="state.hit == 'desc' " style="cursor: pointer; color: #BDFFE7;" @click="handlehit">많은순</label>
                            <label v-if="state.hit == 'asc' " style="cursor: pointer; color: #BDFFE7;" @click="handlehit">적은순</label>
                        </th>
                        <th>
                            <label v-if="state.like == 0">좋아요</label>
                            <label v-if="state.like == 1" style="color:#BDFFE7">좋아요</label>
                        </th>
                    </tr>
                    <tr v-for="tmp of state.list" :key="tmp" class="b_table_article" >
                        <td width="5%">{{tmp.no}}</td>
                        <td width="10%">{{tmp.category}}</td>
                        <td width="40%" style="text-align: left;">
                            <label style="cursor:pointer" @click="SelectOne(tmp.no)">{{tmp.title}}</label> 
                            <label style="color:#F53569"> [{{tmp.totalreply}}]</label>
                        </td>
                        <td width="10%" style="text-align: left;">
                            {{tmp.nickname}}
                        </td>
                        <td width="15%">
                            <div v-if="state.date > tmp.regdate">{{ moment(tmp.regdate).format("MM-DD")}}</div>
                            <div v-if="state.date <= tmp.regdate" style="color:#E87DC3">{{ moment(tmp.regdate).format("HH:mm")}}</div>
                        </td>
                        <td width="10%">{{tmp.hit}}</td>
                        <td width="10%">
                            <div v-if="tmp.boardlike == 0">{{tmp.boardlike}}</div>
                            <div v-if="tmp.boardlike > 0" style="color:#F53569">{{tmp.boardlike}}</div>
                        </td>
                    </tr>
                </table>
                <div class="q-pa-lg flex flex-center">
                    <q-pagination v-model="state.page" color="teal" :max=state.total :max-pages="6" :boundary-numbers="false" @click="handleSearch" />
                </div>
                <div class="b_article_footer">
                    <select>
                        <option>제목</option>
                        <option>아이디</option>
                    </select>    
                    <input type="search" v-model=state.keyword @change="handleSearch" />
                    <q-btn outline icon="search" @click="handleSearch" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { computed, onMounted, reactive } from '@vue/runtime-core';
import axios from 'axios';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
export default {
    setup () {
        const router = useRouter();
        const store = useStore();
        const moment = require('moment');
        
        const state = reactive({
            total : 1,
            page : 1,
            keyword : "",
            category : "",
            list : "",
            hit : "",
            like : 0,
            logged : computed(() => store.getters.getLogged),
            date : moment(new Date()).format("YYYY-MM-DD"),
        });

        const handleSearch = () => {
            handleData();
        }

        const handlehit = () => {
            if(state.hit == ""){
                state.hit = "desc";
            }
            else if(state.hit == "desc"){
                state.hit = "asc";
                console.log(state.hit);
            }
            else {
                state.hit = "";
            }
            handleData();
        }

        const handlelike = () => {
            state.page = 1;
            if(state.like == 0){
                state.like = 1;
            }
            else {
                state.like = 0;
            }
            console.log(state.like);
            handleData();
        }

        const handlecategory = (val) => {
            state.category = val;
            state.keyword = "";
            state.page = 1;
            state.hit = "",
            state.like = 0,
            handleData();
        }

        const handleData = async() => {
            const url = `/COCO/api/board/selectlist?page=${state.page}&keyword=${state.keyword}&category=${state.category}&hit=${state.hit}&like=${state.like}`;
            const headers = {"Content-Type" : "application/json"};

            const {data} = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200){
                state.list = data.result.list;
                state.total = Math.ceil(data.result.total/10);
            }

        }

        const handlewrite = () => {
            if(state.logged){
                router.push({path:'/boardwrite'});  
            }
            else {
                alert('글쓰기 권한이 없습니다.');
            }    
        }

        const SelectOne = async(no) => {
            const url = `/COCO/api/board/updatehit?no=${no}`
            const headers = {"Content-Type":"application/json"};
            const body = {};
            const {data} = await axios.put(url, body, {headers});
            if(data.status === 200){
                router.push({path:'/boardone', query:{no:no}});
            }
        }

        onMounted(() =>{
            handleData();
        });

        return {
            state, 
            handleSearch,
            SelectOne,
            handlewrite,
            handlecategory,
            handlehit,
            handlelike,
            moment
        }
    }
}
</script>

<style lang="scss" scoped>

    .b_wrap {
        padding: 50px;
        display: flex;
        justify-content: center;
    }
    .b_main{
        padding: 100px;
    }

    .b_article_header_btn{
        display: flex;
        justify-content: space-between;
        margin-bottom: 5px;
    }

    .b_table {
        width: 1000px;
        text-align: center;
        font-size: 1.1em;
        border-collapse: collapse;
        border-radius: 0.5em;
        overflow: hidden;
    }
    .b_table_header {
        background-color: #74b59d;
        color: white;
        font-size: 1.2em;
        height: 58px;
    }
    
    .b_table > th,td {
        border-bottom: 1px solid black;
        height: 47px;
    }

    .b_table_article:hover {
        background-color: whitesmoke;
    }

    .b_article_footer {
        padding: 5px;
        display: flex;
        justify-content: center;
    }
    .b_article_footer > input {
        width: 300px;
        border: 1px solid black;
        border-radius: 0.3em;
    }
    .b_article_footer > select {
        width: 80px;
        border: 1px solid black;
        border-radius: 0.3em;
    }

</style>