<template>
  <div>
    <nav class="header row">
      <div class="col-1"></div>
      <div class="col-10">
      <q-toolbar>

        <router-link  to="/" style="padding-left:20px;">
          <div class="logo"></div>
        </router-link>

        <q-space />

        <q-space />

        <q-tabs class="text-secondary text-bold tabs" shrink stretch dense active-color="accent">
          <q-route-tab class="tab" :ripple="false" to="/cours" exact>
            COURS
          </q-route-tab>
          <q-route-tab class="tab" :ripple="false" to="/community" exact>
            COMMUNITY
          </q-route-tab>
          <q-route-tab class="tab" :ripple="false" to="/contact" exact>
            CONTACT US
          </q-route-tab>
        </q-tabs>


        <!-- 검색창 -->
        <div>
          <q-input class="sm-hide" style="margin:0 20px;width: 160px;" v-model="state.text"  
            standout="bg-secondary text-white" dense @keyup.enter="handleSearch"
            @focusin="search = true" @focusout="search=false"
            >
            <template v-slot:append>
              <q-icon v-if="search == false" color="secondary" name="search" @click="handleSearch" />
              <q-icon v-if="search == true" color="white" name="search" @click="handleSearch" />
            </template>
          </q-input>
        </div>
          

        <div v-if="!state.logged" class="app-login">
          <q-btn style="width:90px;" outline color="secondary" @click="handleLoginPage" label="로그인" />
        </div>

        <!-- 마이메뉴 -->
        <div v-if="state.logged" class="row">

          <q-btn style="width:90px;" outline color="secondary"
           @click="handleMemberData" label="마이메뉴" />

            <q-menu transition-show="slide-down" transition-hide="slide-up" class="q-menu" anchor="bottom left" self="top left">                            
              <div class="member-profile no-wrap q-pa-md">

                <div class="row">                

                  <div class="avatar">
                      <div v-if="state.memberInfo.img != null" class="avatar-box" >
                        <q-avatar class="avatar-radius z-max" size="60px" color="white">
                          <q-avatar class="avatar-member" size="60px" >
                            <img :src="`/COCO/api/member/image?no=${state.memberInfo.img}`"/>
                          </q-avatar>
                        </q-avatar>
                      </div>
                      
                      <div v-if="state.memberInfo.img == null" class="avatar-box">
                        <q-avatar class="avatar-radius" size="60px" color="white">
                          <q-avatar class="avatar-member" size="60px">
                            <img :src="state.avatar" />
                          </q-avatar>
                        </q-avatar>
                      </div>                      
                  </div>

                  <div class="id-box text-h6">  
                    <div>
                      {{state.memberInfo.id}}
                    </div>         

                    <div v-if="state.teacherRole" class="text-body2 text-grey-7">
                      {{state.memberInfo.name}} / 강사
                    </div>
                    <div v-else class="text-body2 text-grey-7">
                      {{state.memberInfo.name}} / 학생
                    </div>

                  </div>

                </div>  

                <div class="column">

                  <q-separator />

                  <q-btn align="left" to="/myinfo" icon="manage_accounts" flat outline color="secondary" class="my-btn">
                    &nbsp;내 정보
                  </q-btn>

                  <q-btn align="left" to="/mycours" icon="piano" flat outline color="secondary" class="my-btn">
                    &nbsp;내 강좌
                  </q-btn>

                  <q-btn align="left" to="/chatroom" icon="question_answer" flat outline color="secondary" class="my-btn">
                    &nbsp;1:1 메시지
                  </q-btn>
                         
                  <q-btn v-if="state.newTeacher" align="left" to="/teacher" icon="school" flat outline color="secondary" class="my-btn">
                    &nbsp;강사신청
                  </q-btn>

                  <q-btn v-else-if="state.teacherInfo.steps === 0" to="/teacher" align="left" icon="school" flat outline color="secondary" class="my-btn">
                    &nbsp;강사신청
                  </q-btn>

                  <q-btn v-if="state.teacherInfo.steps === 1" disable  align="left" icon="school" flat outline color="secondary" class="my-btn">
                    &nbsp;강사승인대기
                  </q-btn>

                  <q-btn v-if="state.teacherRole" align="left" icon="school" flat outline color="secondary" class="my-btn">
                      <div @click="handleTeacherVue()">&nbsp;강사페이지</div>
                  </q-btn>

                  <q-btn to="logout" outline color="negative" class="my-btn">
                      Logout
                  </q-btn>
                </div>
                
                
              </div>
            </q-menu>
        </div>

      </q-toolbar>

      </div>

      <div class="col-1"></div>

      </nav>
  </div>    

  <div style="background:transparent; height:50px; width:100%"></div>


  <div style="min-height:900px;">
    <router-view></router-view>
  </div>


  <div class="footer row">
    <div class="col-2"></div>

    <div class="col-4 footer-logo">
        
        <router-link  to="/" style="padding-left:20px; margin-bottom: 5px;" >
          <div class="footerlogo"></div>
        </router-link>

    </div>

    <div class="col-4 footer-text">

        <div class="fontfooter">
          (주)COCONUT | 대표자: 김다솔 | 사업자번호: 000-00-00000<br>
          통신판매업: 2022-부산-00 | 개인정보보호책임자: 김다솔 | 이메일: coconut@coconut.com <br>
          주소: 부산광역시 부산진구 KR 부산광역시 부산진구 동성직업전문학교 A1프라자 6층 <br>
          ©COCONUT. ALL RIGHTS RESERVED
        </div>
      
    </div>

    <div class="col-2"></div>

  </div>
  <q-btn class="up-btn" text-color="secondary" fab icon="expand_less" padding="xs" @click="handleTopScroll" />
</template>

<script>
import { reactive, ref, toRefs } from '@vue/reactivity';
import { useStore } from 'vuex';
import { computed } from '@vue/runtime-core';
import { useRouter } from 'vue-router';
export default {
  setup() {
    const store = useStore();
    const router = useRouter();
    const state = reactive({
      logged : computed(() => store.getters.getLogged),
      id : '',
      pw : '',
      token : sessionStorage.getItem("TOKEN"),
      teacherRole : computed(() => store.getters.getTeacherRole),
      appToken : computed(() => store.getters.getAppToken),
      text : '',
      memberInfo : computed(() => store.getters.getMemberInfo),
      teacherInfo : computed(() => store.getters.getTeacherInfo),
      newTeacher : computed(() => store.getters.getNewTeacher),
      avatar : require('@/assets/images/logo/coconut_500.png')
    });

    const handleSearch = async() => {
      store.commit('setMainText', state.text)
      router.push({path:'/cours'})
    }

    const handleLoginPage = () => {
      router.push({path:'/login'})
    }

    const handleCours = () => {
      router.push({path:'/cours'})
    }

    const handleCommunity = () => {
      router.push({path:'/community'})
    }

    const handleMemberData = () => {
      store.dispatch('handleMemberData');
      store.dispatch('handleTeacherData');
      store.dispatch('handleTeacherRole');
    }

    const handleTeacherApply = () => {
      if(state.teacherInfo.steps === 1) {
        alert('신청 승인 대기중입니다');
        return false;
      }
      else {

        if(state.newTeacher === true) {
          router.push({path: '/teacher'})
        }
        else {
          router.push({path: '/tchrinfo'})
        }
      }
    }

    const handleTeacherVue = () => {
      // 빌드전
      // window.location.href = ("http://localhost:9091?token="+sessionStorage.getItem("TOKEN"));
      //빌드후
      // window.location.href = ("http://127.0.0.1:8081/COCO/teacher.do");
      //배포후
      window.location.href = ("http://3.35.91.60:8080/COCO/teacher.do");
      
    }

    const handleChat = () => { // 아이디별 채팅방 이동
        router.push({path : '/chatroom'});
    }

    const handleTopScroll = () => {
      window.scrollTo({ left: 0, top: 0, behavior: "smooth" });
    }

    return {
      state,
      handleSearch,
      handleLoginPage,
      handleTeacherVue,
      handleCours,
      handleCommunity,
      handleMemberData,
      handleChat,
      handleTeacherApply,
      ...toRefs(state),
      search : ref(false),
      handleTopScroll
    }
  }
}
</script>



<style lang="css" scoped src="@/assets/css/App.css">

</style>

