import axios from "axios";
import { createStore } from "vuex";

export default createStore({
    state() {

        let tmp = false;
        if(sessionStorage.getItem("TOKEN") !== null){
            tmp = true;
        }
        return {
            logged : tmp,
            appToken : sessionStorage.getItem("TOKEN"),
            teacherRole : false,
            mainText : null,
            loginUrl : false,
            memberInfo : [],
            mainGnb : false,
            modalOrder : false,
            teacherInfo : [],
            newTeacher : false,
            categorySearch : ''
        }
    },

    getters : {
        getLogged(state) {return state.logged},
        getAppToken(state) {return state.appToken},
        getTeacherRole(state) {return state.teacherRole},
        getMainText(state) {return state.mainText},
        getLoginUrl(state) {return state.loginUrl},
        getMemberInfo(state) {return state.memberInfo},
        getMainGnb(state) {return state.mainGnb},
        getModalOrder(state) {return state.modalOrder},
        getTeacherInfo(state) {return state.teacherInfo},
        getNewTeacher(state) {return state.newTeacher},
        getCategorySearch(state) {return state.categorySearch}
    },

    mutations : {
        setLogged(state, value) {state.logged = value},
        setAppToken(state, value) {state.appToken = value},
        setTeacherRole(state, value) {state.teacherRole = value},
        setMainText(state, value) {state.mainText = value},
        setLoginUrl(state, value) {state.loginUrl = value},
        setMemberInfo(state, value) {state.memberInfo = value},
        setMainGnb(state, value) {state.mainGnb = value},
        setModalOrder(state, value) {state.modalOrder = value},
        setTeacherInfo(state, value) {state.teacherInfo = value},
        setNewTeacher(state, value) {state.newTeacher = value},
        setCategorySearch(state, value) {state.categorySearch = value}
    },

    actions : {
        async handleMemberData(context) {
            const url = `/COCO/api/member/selectone.json`;
            const headers = {
            "Content-Type":"application/json",
            "TOKEN" : sessionStorage.getItem("TOKEN")
        };
        if(sessionStorage.getItem("TOKEN") !== null) {
            const { data } = await axios.get(url, {headers});
            console.log('회원조회',data);
            context.commit('setMemberInfo', data.result)
            }
        },

        async handleTeacherData(context) {
            const url = `/COCO/api/teacher/selectOneteacher.json`;
            const headers = {
                "Content-Type":"application/json",
                "TOKEN" : sessionStorage.getItem("TOKEN")
            };
            if(sessionStorage.getItem("TOKEN") !== null) {
                const { data } = await axios.get(url, {headers});
                console.log('강사조회',data);
                if(data.status === 200) {
                    context.commit('setTeacherInfo', data.result);
                    context.commit('setNewTeacher', false)
                }
                else {
                    context.commit('setNewTeacher', true)
                }
            }
        },

        async handleTeacherRole(context) {
            const url = `/COCO/api/member/selectonerole.json`;
            const headers = {
                "Content-Type":"application/json",
                "TOKEN" : sessionStorage.getItem("TOKEN")
            };
            if(sessionStorage.getItem("TOKEN") !== null) {
                const { data } = await axios.get(url, {headers})
                console.log('Role 조회', data);
                if(data.status === 200) {
                    let str = data.result;
                    if(str.includes('TEACHER')) {
                        context.commit('setTeacherRole', true)
                    }
                    else {
                        context.commit('setTeacherRole', false)
                    }
                }
            }
        }

    }
});