<template>
    <div style="padding:20px;">
        <h6>구매내역</h6>
        {{state.courslist}} <br /><br />
        <div v-for="tmp in state.courslist" :key="tmp">
            <input type="text" v-model="tmp.title" readonly @click="handleSelectOneCours(tmp.coursno)" />
        </div>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import axios from 'axios'
import { onMounted } from '@vue/runtime-core'
import { useRouter } from 'vue-router'
export default {
    setup() {
        const router = useRouter();
        const state = reactive({
            token : sessionStorage.getItem("TOKEN"),
            courslist : []
        })

        const handleData = async() => {
            const url = `/COCO/api/order/select.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            }
            const { data } = await axios.get(url, {headers});
            console.log(data)
            if(data.status === 200) {
                state.courslist = data.courslist;
            }
        };

        const handleSelectOneCours = (obj) => {
            router.push({path: '/coursone', query: {coursno : obj }})
        }

        onMounted(() => {
            handleData();
        })

        return{
            state,
            handleSelectOneCours
        }
    }
}
</script>

