<template>
    <div class="container">
        <!-- <div class="text-h5 title-text">이미지 추가</div> -->

        <div class="q-pa-md row items-start q-gutter-md">
            <q-card class="my-card">

            <q-card-section>
                <div class="text-h6 text-secondary">이미지 추가</div>
            </q-card-section>

            <div v-if="!state.imageList.length > 0" style="padding:15px" >
                <!-- <img src="@/assets/images/no_image.png"  style="width:100px"/> -->
                <div class="text-caption text-grey">등록된 이미지가 없습니다.</div>
                <div class="text-caption text-grey">최대 3개까지 추가할 수 있습니다.</div>
            </div>    

            <div>
                <table border="0" style="height:260px;">
                    <tbody>
                        <tr v-for="tmp of state.imageList" :key="tmp" style="display:inline-block; padding: 15px;">
                                <td hidden>{{tmp.teacherimageno}}</td>
                                <td><img :src="`/COCO/api/teacher/image.json?teacherno=${tmp.teacherimageno}`" style="max-width:200px; height:200px"></td>
                            <div align="center">
                                <td style="margin-top:-20px">
                                    <q-btn flat size="sm" color="grey" icon="delete" label="delete" @click="handleDeleteImage(tmp.teacherimageno)" />
                                </td>
                            </div>
                        </tr>
                    </tbody>
                </table>
            </div>

            <q-card-actions>
                <q-btn class="btn-frame" dense color="white" text-color="black" label="뒤로" @click="handlePrev" />
                <q-btn class="btn-frame" dense color="primary" label="다음" @click="handleNext" />

                <q-space />

                <q-btn color="grey" flat dense label="이미지 추가" :icon="expanded ? 'keyboard_arrow_up' : 'keyboard_arrow_down'" @click="expanded = !expanded" />
            </q-card-actions>

            <q-slide-transition>
                <div v-show="expanded">
                <q-separator />

                <q-card flat class="input-card">
                    <img :src="state.imageurl" style="width:170px; padding: .1em .75em;" />
                    <q-item>
                        <q-item-section v-if="!state.inputBtn" align="center">
                            <div class="file-box" style="display:inline-block;">
                                
                                    <label for="input-file">파일선택</label>
                                    <input type="file" id="input-file" @change="handleImage($event)" style="display:none" />
                                
                            </div>
                        </q-item-section>

                        <q-item-section v-if="state.inputBtn" align="center" >
                            <div style="display:inline-block;">
                            <!-- <label  @click="handleInsertImage">추가</label> -->
                            <q-btn flat dense label="취소" icon="close" text-color="grey" @click="handleImageCancer"></q-btn>
                            <q-btn flat dense label="추가" icon="cloud_upload" color="primary" v-if="state.inputBtn" @click="handleInsertImage"></q-btn>
                            </div>
                        </q-item-section>
                    </q-item>
                </q-card>
                
                </div>
            </q-slide-transition>
            </q-card>
        </div>

    </div>
</template>

<script>
import { reactive, ref } from '@vue/reactivity';
import axios from 'axios';
import { onMounted } from '@vue/runtime-core';
import { useRouter } from 'vue-router';
export default {
    setup() {
        const router = useRouter();
        const state = reactive({
            token     : sessionStorage.getItem("TOKEN"),
            image     : [],
            imageBtn  : false,
            imageB    : false,
            imageList : [],
            imageurl  : require('@/assets/images/no_image.png'),
            img       : '',
            file      : '',
            inputBtn : false
        })

        const handlePrev = () => {
            router.push({path:'/tchrinfo'})
        }

        const handleNext = () => {
            router.push({path:'/tchredu'})
        }

        const handleImageBtn = () => {
            state.imageBtn = !state.imageBtn;
            // state.imageB = true;
        }

        // ----------------------------------------------------------------이미지 삭제-----------------------------------------------------
        const handleDeleteImage = async(teacherimageno) => {
            const url = `/COCO/api/teacher/deleteimage.json?teacherimageno=${teacherimageno}`;
            const headers = {
                "Content-Type" : "application/json", "TOKEN" : state.token
            };
            if(confirm('삭제하시겠습니까?')){
                const {data} = await axios.post(url, {}, {headers});
                console.log(data);
                handleImageData();
            }
        }
        
        // ----------------------------------------------------------------이미지 등록---------------------------------------------------
        const handleInsertImage = async() => {
            if(state.imageList.length >= 3) {
                alert('이미지는 최대 3개까지만 추가 가능합니다.')
            }
            else {
                const url = `/COCO/api/teacher/joinimage.json`;
                const headers = {
                    "Content-Type":"multipart/form-data",
                    "TOKEN" : state.token
                };
                let body = new FormData();
                body.append("file", state.file);
                const {data} = await axios.post(url, body, {headers});
                console.log(data);
                state.inputBtn = false;
                state.imageurl = require('@/assets/images/no_image.png');
                handleImageData();
            }
        }

        // -----------------------------------------------------------------이미지 조회--------------------------------------------------
        // const handleSelectImage = async(teacherimageno) => {
        //     const url = `/COCO/api/teacher/image.json?teacherno=${teacherimageno}`
        //     const headers = {"Content-Type":"application/json", "TOKEN" : state.token};
        //     const {data} = await axios.get(url, {headers});
        //     console.log(data);
        //     state.img = `/COCO/api/teacher/image.json?teacherno=${teacherimageno}`
        // }
        
        // ----------------------------------------------------------------이미지조회-----------------------------------------------------
        const handleImageData = async() => {
            const url = `/COCO/api/teacher/selectimage.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            }
            const { data } = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200) {
                state.imageList = data.image;
            }
        }

        const handleImage = (e) => {
            console.log(e.target.files[0]);
            console.log(e.target.files.length);
            if(e.target.files.length > 0){
                state.file = e.target.files[0];
                state.imageurl  = URL.createObjectURL( e.target.files[0] );
                state.inputBtn = true;
            }
            else {
                state.file = '';
                state.imageurl = require('@/assets/images/no_image.png');
            }
        };

        const handleImageCancer = () => {
            state.file = '';
            state.imageurl = require('@/assets/images/no_image.png');
            state.inputBtn = false
        }

    onMounted(() => {
        handleImageData();
    })

        return {
            state,
            handleNext,
            handleImageBtn,
            handleImage,
            handleInsertImage,
            handleDeleteImage,
            handleImageCancer,
            handlePrev,
            expanded: ref(false),
            files: ref(null)
        }
    }
}
</script>

<style lang="css" scoped src="@/assets/css/TeacherPage.css">
.input-btn {
    color : #4d9056;
    background-color: red;
}
</style>