<template>
    <div>
        <div class="q-pa-md">
            <q-btn style="background: #74b59d; color: white" push @click="handleJoin()" label="글쓰기" />
            <br />
            <br />
            <q-table title="1:1 문의 게시판" :rows="state.qnadate" :columns="columns" row-key="qnano" @row-click="onRowClick" />
        </div>
        
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import axios from 'axios';
import router from '@/routes'
import { onMounted } from '@vue/runtime-core';
export default {
    methods: {
        onRowClick (evt, qnadate) {
            console.log('clicked on', evt, qnadate)
            router.push({path:'/content', query:{qnano : qnadate.qnano}})
        }
    },
    props : {
        title : String
    },
    setup (props, {emit}) {
        const columns = [
            // { name: 'qnano', label: '번호', align: 'left', field: qnadate => qnadate.qnano, format: val => `${val}`, sortable: true },
            { name: 'title', required: true, label: '제목', align: 'left', field: qnadate => qnadate.title, format: val => `${val}`, sortable: true },
            { name: 'type', label: '유형', align: 'left', field: qnadate => qnadate.type, sortable: true },
            { name: 'content', label: '내용', align: 'left', field: qnadate => qnadate.content, sortable: false },
            { name: 'status', label: '상태', align: 'left', field: qnadate => qnadate.status, sortable: false },
            { name: 'regdate', label: '작성일', align: 'left', field: qnadate => qnadate.regdate, sortable: true },
        ]
        // const router = useRouter();
        const state = reactive({
            qnadate : [],
            title : props.title,
            token : sessionStorage.getItem("TOKEN")
        });

        onMounted(() => {
            handleData();
        });

        const handleData = async () => {
            const url = `/COCO/api/qna/select.json`;
            const headers = {"Content-Type":"application/json", "token" : state.token};
            const {data} = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200){
                state.qnadate = data.qna;
            }
        };

        const handleJoin = async () => {
            router.push({path : '/write'});
        };

        const handleReply = async (qnano) => {
            router.push({path: '/content', query : {qnano : qnano}});
        };

        const handleEmit = () => {
            // 부모쪽으로 이벤트 발생
            emit('sendData',{key:'a', value:'b'});
        };

        return {state, handleEmit, handleJoin, handleReply, columns};
    }
}
</script>

