<template>
    <div class="q-pa-md row items-start q-gutter-md">
        <q-card class="my-card" flat>
            <q-card-section>
            <div class="text-h6 text-secondary">답변</div>
        </q-card-section>

        <div class="q-pa-md" style="margin-top:-20px;">
            <q-input outlined type="textarea" color="secondary" v-model="state.qnadata.content" label="질문"
            style="height: 340px, width : 200px, margin-top:-20px;" counter maxlength="1000" readonly/>
            <q-input outlined type="textarea" color="secondary" v-model="state.content" label="답변 작성"
            style="height: 340px, width : 200px, margin-top:-20px;" counter maxlength="1000" />
            <div style="margin-top:20px;">
                <q-btn class="btn-frame" dense color="green" label="뒤로가기" @click="handleReturn" />
                <q-btn class="btn-frame" dense color="green" label="입력" @click="handleInsert" />
            </div>
        </div>
        </q-card>

    </div>
</template>

<script>
import { reactive } from '@vue/reactivity';
import { onMounted } from '@vue/runtime-core';
import axios from 'axios';
import { useRoute, useRouter } from 'vue-router';
export default {
    setup () {
        const router = useRouter();
        const route = useRoute();
        const state = reactive({
            qnano : Number(route.query.qnano),
            qnadata : '',
            content : '',
            token : sessionStorage.getItem("TOKEN")
        });

        onMounted(() => {
            handleData();
        });

        const handleData = async () => {
            const url = `/COCO/api/qna/selectone.json?qnano=${state.qnano}`;
            const headers = {"Content-Type":"application/json"};
            const {data} = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200){
                state.qnadata = data.qna;
            }
        };

        const handleInsert = async () => {
            const url = `/COCO/api/reply/insert.json?qnano=${state.qnano}`;
            const headers = {"Content-Type":"application/json", "token" : state.token};
            const body = {content : state.content};
            const {data} = await axios.post(url, body, {headers});
            console.log(data);
            if(data.status === 200){
                alert('등록 완료!');
                router.push({path : '/content', query : {qnano : state.qnano}});
            }
        };

        const handleReturn = async () => {
            router.push({path : '/contact'});
        };

        return {state, handleInsert, handleReturn};
    }
}
</script>

