<template>
    <div class="container">

        <div class="q-pa-md row items-start q-gutter-md">
            <q-card class="my-card" flat>

            <q-card-section>
                <div class="text-h6 text-secondary">학력</div>
            </q-card-section>

            <!-- <div class="text-h5 title-text">학력</div> -->

            <div v-if="!state.eduList.length > 0" style="padding:15px" >
                <div class="text-caption text-grey">등록된 학력이 없습니다.</div>
                <div class="text-caption text-grey">최대 3개까지 추가할 수 있습니다.</div>
            </div>    

            <div>
                <q-card flat>
                    <q-item>
                        <q-item-section class="vfor-box">
                            <div v-for="tmp of state.eduList" :key="tmp" class="row" style="padding: 15px;">
                                <q-card class="row">

                                    <q-card-section class="row">
                                        <div hidden>{{tmp.educationno}}</div>
                                        <div style="width:160px;">{{tmp.name}}</div>
                                        <div style="width:160px;">{{tmp.department}}</div>
                                        <div style="width:60px;">{{tmp.status}}</div>
                                    </q-card-section>

                                    <q-separator vertical />

                                    <q-card-actions>
                                        <div><q-btn color="secondary" flat dense @click="handleAuthEdu(tmp.educationno)" label="인증" /></div>
                                        <div><q-btn color="negative" flat dense @click="handleDeleteEdu(tmp.educationno)" label="삭제" /></div>
                                    </q-card-actions>

                                </q-card> 
                            </div>
                        </q-item-section>
                    </q-item>
                </q-card>
            </div>

            <q-card-actions>
                <q-btn class="btn-frame" dense color="white" text-color="black" label="뒤로" @click="handlePrev" />
                <q-btn class="btn-frame" dense color="primary" label="다음" @click="handleNext" />

                <q-space />

                <q-btn color="grey" flat dense label="학력 추가" :icon="expanded ? 'keyboard_arrow_up' : 'keyboard_arrow_down'" @click="expanded = !expanded" />
            </q-card-actions>

            <q-slide-transition>
                <div v-show="expanded">
                    <div class="q-pa-md row">
                        <q-input color="secondary" style="width:160px;" dense v-model="state.education.name" class="input-item" autofocus label="학교/학원" />
                        <q-input color="secondary" style="width:160px;" dense v-model="state.education.department" class="input-item" label="학과" />
                        <q-select color="secondary" style="width:60px;" options-dense dense v-model="state.status" :options="options" class="input-item" label="상태" />
                        <q-btn flat dense color="primary" label="추가" @click="handleInsertEdu" />
                    </div>
                </div>
            </q-slide-transition>
            </q-card>
        </div>

    </div>
</template>

<script>
import { reactive, ref } from '@vue/reactivity';
import { useRouter } from 'vue-router'
import { onMounted } from '@vue/runtime-core';
import axios from 'axios';
export default {
    setup() {
        const router = useRouter();
        const state = reactive({
            token : sessionStorage.getItem("TOKEN"),
            education : [],
            eduList : [],
            status : ''
        })

         // ----------------------------------------------------------------학력조회-----------------------------------------------------
         const handleEduData = async() => {
            const url = `/COCO/api/teacher/selectListeducation.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            }
            const { data } = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200) {
                state.eduList = data.education;
            }
        }

        // ----------------------------------------------------------------학력등록-----------------------------------------------------
        const handleInsertEdu = async() => {        
            if(state.eduList.length >= 3) {
                alert('학력은 최대 3개까지만 추가 가능합니다.')
            }
            else {
                const url = `/COCO/api/teacher/joineducation.json`;
                const headers = {
                    "Content-Type" : "application/json",
                    "TOKEN" : state.token
                };
                const body = {
                    name : state.education.name,
                    department : state.education.department,
                    status : state.status
                }
                
                const { data } = await axios.post(url, body, {headers});
                console.log(data)
                if(data.status === 200) {
                    handleEduData();
                }
            }
        }
        
        // ----------------------------------------------------------------학력 삭제-----------------------------------------------------
        const handleDeleteEdu = async(educationno) => {
            const url = `/COCO/api/teacher/deleteeducation.json?educationno=${educationno}`;
            const headers = {
                "Content-Type" : "application/json", "TOKEN" : state.token
            };
            if(confirm('삭제하시겠습니까?')){
                const {data} = await axios.post(url, {}, {headers});
                console.log(data);
                handleEduData();
            }
        }

        const handlePrev = () => {
            router.push({path:'/tchrimage'})
        }

        const handleNext = () => {
            router.push({path: '/tchrlink'})
        }

        onMounted(() => {
            handleEduData();
        })




        return {
            state,
            handleInsertEdu,
            handleDeleteEdu,
            handleNext,
            handlePrev,
            expanded: ref(false),
            options: ['재학', '졸업', '중퇴']
        }
    }
}
</script>

<style lang="css" scoped src="@/assets/css/TeacherPage.css">

</style>