<template>
    <div class="b_wrap">
      <div class="b_article">
        <div class="b_art_header">
          <input v-model="title"/>
          <select v-model="category">
              <option>자유</option>
              <option>질문</option>
              <option>중고거래</option>
          </select>    
        </div>
        <hr />
        <editor v-model="content" />
    
        <div class="b_art_footer">
          <q-btn class="b_art_footer_btn_update" flat style="background: #74b59d; color: white; width: 80px;" @click="writeBoard" icon="edit" size="20px" />
          <q-btn class="b_art_footer_btn_cancel" flat style="background: #B57162; color: white; width: 80px;" @click="cancelBoard" icon="close" size="20px" />
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  import { useRouter } from 'vue-router';
  
  import Editor from '@/components/editor/boardeditor/BoardEditor.vue'
import { ref } from '@vue/reactivity';
  
  export default {
      setup () {
      return {
        category : ref("자유"),
        options: [
          '자유', '질문', '중고거래'
        ]
      }
    },
      components: {
        Editor,
      },
  
      data() {
        return {
          title : "",
          content : '<p></p><p></p><p></p><p></p><p></p><p></p><p></p><p></p>',
          router : useRouter()
        }
      },
  
      methods : {
        writeBoard : async function(){
          console.log(this.content);
          const url = `/COCO/api/board/insert.json`;
              const headers = {
                "Content-Type":"application/json",
                "TOKEN":sessionStorage.getItem("TOKEN")
              };
              const body = {
                  title   : this.title,
                  content : this.content,
                  category : this.category,
              }
              const {data} = await axios.post(url, body, {headers});
              console.log(data);
              if(data.status === 200){
                this.router.push({path:'/community'});  
              }
        },
        cancelBoard : function(){
          this.router.push({path:'/community'});
        }
      },
    }
    
  
  </script>
  
  <style lang="scss" scoped>
  .b_wrap {
    padding: 50px;
    display: flex;
    justify-content: center;
  }
  .b_article {
    width: 780px;
  }
  .b_art_header {
    display: flex;
    justify-content: space-between;
    height: 50px;
  }
  .b_art_header > input {
    width: 600px;
    border: 3px solid #74b59d;
    border-radius: 0.5em;
    font-size: 21px;
  }
  .b_art_header > select {
    width: 150px;
    border: 3px solid #74b59d;
    border-radius: 0.5em;
    font-size: 15px;
    font-weight: 600;
    color: #74b59d;
  }
  .b_art_footer {
    padding-top: 10px;
    display: flex;
    justify-content: flex-end;
    column-gap: 5px;
  }
  </style>