import { createApp } from 'vue'
import App from './App.vue'

import routes from './routes/index';
import stores from './stores/index';
import Quasar from 'quasar/src/vue-plugin.js';import Dialog from 'quasar/src/plugins/Dialog.js';
import quasarUserOptions from './quasar-user-options'
// import IconBase from '@/components/reference/IconBase.vue'
// import IconYoutube from '@/components/reference/icon/IconYoutube.vue'

createApp(App)
.use(Quasar, {
  plugins: {
    Dialog
  },
  config: {
    brand: {
      primary: '#1976d2',
      secondary: '#4d9056',
      accent: '#90564d',
      
      dark: '#1d1d1d',
      'dark-page': '#121212',
      
      positive: '#18ff11',
      negative: '#C10015',
      info: '#31CCEC',
      warning: '#F2C037'
    }
  }
}, quasarUserOptions)
.use(routes).use(stores)
// .component('icon-base', IconBase).component('icon-youtube', IconYoutube)
.mount('#app');
