<template>
    <div class="back-back">

        <div class="flex flex-center">

            <q-card style="width:400px;margin-top:150px; opacity: 0.85;">
                <q-tabs v-model="tab" active-bg-color="white" indicator-color="transparent"
                    class="bg-grey-4 text-black"  style="height:40px;">

                <q-tab name="userid" :ripple="false" label="아이디 찾기" />
                <q-tab name="userpw" :ripple="false" label="비밀번호 찾기" />

                </q-tabs>


                    <q-tab-panels v-model="tab" animated class="bg-white text-center">

                        <q-tab-panel name="userid" align="center">
                            
                            <q-input class="element" color="secondary" dense outlined placeholder="이메일을 입력하세요" 
                                v-model="state.email1" style="width:80%; margin:35px 0;" hide-bottom-space :maxlength="50"/>

                            <q-btn class="element" @click="FindId" color="secondary" style="width:80%" label="아이디찾기" />
                            
                        </q-tab-panel>

                        <q-tab-panel name="userpw" align="center" class="bg-white">

                            <q-input class="element" color="secondary" dense outlined placeholder="아이디를 입력하세요"
                                v-model="state.id" style="width:80%" hide-bottom-space :maxlength="13"/>

                            <q-input class="element" color="secondary" dense outlined placeholder="이메일을 입력하세요"
                            v-model="state.email2" style="width:80%" hide-bottom-space :maxlength="50"/>

                            <q-btn class="element" @click="FindPw" color="secondary" style="width:80%">비밀번호 찾기</q-btn>
                        
                        </q-tab-panel>

                </q-tab-panels>
            </q-card>

        </div>
    </div>
</template>

<script>
import { reactive, ref } from '@vue/reactivity';
import axios from 'axios';
export default {
    setup () {
        const state = reactive({
            id : '',
            email1 : '',
            email2 : '',
    }); 

    const FindId = async() => {
        // if(state.email1 === null) {
        //     alert('이메일을 입력하세요.');
        //     return false;
        // }
        const url = `/COCO/api/member/findId.json`;
        const headers = {"Content-Type":"application/json"};
        const body = {
            email : state.email1,
        };
        const {data} = await axios.post(url, body, {headers});
        alert('아이디는 ' + data.result + ' 입니다.');
    }

    const FindPw = async() => {
        const url = `/COCO/api/member/mailConfirm`;
        const headers = {"Content-Type":"application/json"};
        const body = {
            id : state.id,
            email : state.email2,
        };
        const {data} = await axios.post(url, body, {headers});
        console.log(data);
        if(data.status === 200) {
            alert('임시비밀번호를 이메일로 전송하였습니다.')
            state.id = '';
            state.email1 = '';
            state.email2 = '';
        }
    }


        
        return {
            state,
            FindId,
            FindPw,
            tab: ref('userid')
        }
    }
}
</script>

<style lang="css" scoped src="@/assets/css/Sign.css">

</style>