<template>
    <div class="container">
        <h3>board</h3>
        <hr />
        <div id="board">
        </div>
        <h3>boardreply</h3>
        <hr />
        <div id="boardreply">
            <div v-for="tmp of state.boardreply" :key="tmp" >
                {{tmp}}
            </div>
            <div class="q-pa-lg flex flex-center">
                <q-pagination color="black" v-model="state.page" :max=state.total @click="handleData2" />
            </div>
        </div>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity';
import { onMounted } from '@vue/runtime-core';
import axios from 'axios';
import { useRoute } from 'vue-router';
export default {
    setup () {
        const route = useRoute();
        const state = reactive({
            board : "<p>The Content</p>",
            no : Number(route.query.no),
            content : "",
            boardreply : "",
            page : 1,
            total : 1,
        });

        const handleData = async () =>{
            const url = `/COCO/api/board/selectone?no=`+state.no;
            const headers = {"Content-Type" : "application/json"};

            const { data } = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200){
                state.content = data.result.content;
                document.getElementById("board").innerHTML = state.content;
            }
        }

        const handleData2 = async () =>{
            const url = `/COCO/api/boardreply/selectlist.json`;
            const headers = {"Content-Type" : "application/json"};
            const body = {
                "boardno" : state.no,
                "page" : state.page
            };

            const { data } = await axios.post(url, body, {headers});
            console.log(data);
            if(data.status === 200){
                state.boardreply = data.result.boardreply;
                state.total = Math.ceil(data.result.total/5);
            } 
        }

        onMounted(()=>{
            handleData();
            handleData2();
        })

        return {state, handleData2}
    }
}
</script>

<style lang="scss" scoped>
    .container{
        padding: 50px;
    }
</style>