<template>
    <div class="q-pa-md">
        <div id="board"></div>
    </div>
</template>

<script>
import { reactive, toRefs } from '@vue/reactivity'
import { onMounted } from '@vue/runtime-core'
export default {
    props : {
        intro : String
    },
    setup (props) {
        const state = reactive({
            intro : props.intro
        })

        onMounted(() => {
            document.getElementById("board").innerHTML = state.intro
        })

        return {
            state,
            ...toRefs(state),
        }
    }
}
</script>

