<template>
    <div class="row" >

        <div class="col-2"></div>

        <div class="col-8">

            <br />
            <q-tabs v-model="tab" dense align="left" class=" text-secondary" :breakpoint="0">
                <q-tab name="order" label="구매내역" @click="menu = 1" />
                <q-tab name="wish" label="관심강좌" @click="menu = 2" />
                <q-tab name="review" label="마이리뷰" @click="menu = 3" />
                <q-tab name="comment" label="내 게시글" @click="menu = 4" />
            </q-tabs>
            <br />

            <div v-if="menu === 1">

                <div v-if="state.noOrder" class="q-pa-md text-center text-body1">
                    구매한 강좌가 없습니다.
                </div>

                <q-intersection once transition="scale" v-for="(tmp, idx) in state.orderList" :key="idx" style="display: inline-block;vertical-align: middle;">
                    <q-card flat class="cours-cardp" align="center" style="margin:10px;">
                        <q-card-section class="cours-imgp" align="center" @click="handleSelectOneCours(tmp.coursno)">
                            <img :src="`/COCO/api/cours/image?no=${tmp.imageno}`" class="img-src" :ratio="4/3" />
                        </q-card-section>

                        <div align="left" class="cours-infop">
                                
                                <div class="text-h6 hov title" style="height: 45px; line-height: 120%;">{{tmp.title}}</div>
                                
                                <div class="hov tchr text-overline">{{tmp.teachername}}</div>
                            
                                <div class="row">
                                    <q-icon style="position:relative; top:2px;" size="1.2em" name="equalizer" />
                                    <span>&nbsp;{{tmp.instrument}}&nbsp;/</span>
                                    <span>&nbsp;{{tmp.difficult}}</span>
                                </div>

                                <q-btn align="center" color="secondary" dense label="레슨하러 가기"
                                    style="width:97%;margin-left:0px; margin-top:5px;"
                                    @click="handleSelectOneStudy(tmp.coursno)"
                                    />
                        </div>
                    </q-card>
                </q-intersection>
            </div>


            
            <div v-if="menu === 2">

                <div v-if="state.noWish" class="q-pa-md text-center text-body1">
                    관심 강좌가 없습니다.
                </div>
                <div v-if="!state.noWish">
                    <q-intersection once transition="scale" v-for="tmp in state.wishList" :key="tmp" style="display: inline-block;vertical-align: middle;">
                        <q-card flat class="cours-cardw" align="center" style="margin:10px;">
                            <q-card-section class="cours-imgp" align="center" @click="handleSelectOneCours(tmp.coursno)">
                                <img :src="`/COCO/api/cours/image?no=${tmp.iagmeno}`" class="img-src" :ratio="4/3" />
                            </q-card-section>
    
                            <div align="left" class="cours-infop">
                                    
                                <div class="text-h6 hov title" style="height: 45px; line-height: 120%;">{{tmp.title}}</div>
                                
                                <div class="hov tchr text-overline">{{tmp.teachername}}</div>
                            
                                <div class="row">
                                    <q-icon style="position:relative; top:2px;" size="1.2em" name="equalizer" />
                                    <span>&nbsp;{{tmp.instrument}}&nbsp;/</span>
                                    <span>&nbsp;{{tmp.difficult}}</span>
                                </div> 

                                <q-btn align="center" outline color="negative" dense label="관심강좌 삭제"
                                    style="width:97%;margin-left:0px; margin-top:5px;" 
                                    @click="handleDeleteWish(tmp.wishno)"
                                    />
                            </div>
                        </q-card>
    
                    </q-intersection>

                </div>
            </div>

            <menu-3 v-if="menu === 3">

            </menu-3>

            <menu-4 v-if="menu === 4">

            </menu-4>
            

        </div>

        <div class="col-2"></div>

    </div>

</template>

<script>
import { reactive, ref, toRefs } from '@vue/reactivity'
import axios from 'axios'
import { onMounted } from '@vue/runtime-core'
import { useRouter } from 'vue-router'
import Menu3 from '@/components/member/MyReviewPage.vue';
import Menu4 from '@/components/member/MyCommentPage.vue'
export default {
    components : {
        Menu3,
        Menu4
    },
    setup() {
        const router = useRouter();
        const state = reactive({
            token : sessionStorage.getItem("TOKEN"),
            wishList : [],
            orderList : [],
            menu : 1,
            noWish : false,
            noOrder : false
        })

        const handleWishData = async() => {
            const url = `/COCO/api/wish/selectwish.json`;
            const headers = {
                "Content-Type":"multipart/form-data",
                "TOKEN" : state.token
            };
            const { data } = await axios.get(url, {headers})
            console.log('위시내역', data)
            if(data.status === 200) {
                state.wishList = data.wish
                state.noWish = false
            }
            else if(data.status === 0) {
                state.noWish = true;
            }
        }

        const handleOrderData = async() => {
            const url = `/COCO/api/solda/selectorder.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            }
            const { data } = await axios.get(url, {headers});
            console.log('구매내역', data)
            if(data.status === 200) {
                state.orderList = data.order;
            }
            else if(data.status === 0) {
                state.noOrder = true;
            }
        }

        const handleDeleteWish = async(obj) => {
            if(confirm('삭제하시겠습니까?')) {
                const url = `/COCO/api/wish/delete.json?wishno=${obj}`;
                const headers = {
                    "Content-Type":"application/json"
                }
                const { data } = await axios.post(url, {}, {headers});
                console.log('좋아요 비활성',data);
                    alert('삭제되었습니다.')
                    handleWishData();
            }
        }

        const handleSelectOneStudy = (obj) => {
            router.push({path:'/study', query : {coursno : obj}})
        }

        const handleSelectOneCours = (obj) => {
            router.push({path:'/coursone', query :{coursno : obj}})
        }

        const handleReviewCours = () => {
            alert('리뷰 체크 필요')
        }

        onMounted(() => {
            handleWishData();
            handleOrderData();
        })

        return {
            state,
            ...toRefs(state),
            tab : ref('order'),
            handleReviewCours,
            handleSelectOneCours,
            handleSelectOneStudy,
            handleDeleteWish
        }
    }
}
</script>

<style lang="css" scoped>
.wish-box:hover {
    cursor:pointer;
}
.tag-box {
    height: 55px;
    display: none;
}
.cours-cardp {
    width:220px;
    /* margin:10px; */
    /* margin-top: 50px; */
    overflow: hidden;
}
.cours-cardw {
    width:220px;
    /* margin:10px; */
    /* margin-top: 50px; */
    overflow: hidden;
}
.cours-imgp:hover {
    cursor: pointer;
}
.title {
    font-family: sans-serif;
    font-weight: 700;
}
.tchr {
    color:grey;
    /* font-size:larger; */
}
.cours-imgp {
    padding: 0;
    /* transition: all 0.2s linear; */
}
.img-src {
    width:220px;
    height:150px;
    margin-left: -17px;
    /* padding:7px; */
    /* border : 0; */
    /* border-radius: 5%; */
    /* background-color: red; */
}
.cours-infop {
    margin-left: 0px;
    height:140px;
    padding:0;
    /* transition: all 0.2s linear; */
}

</style>