<template>
    <!-- <div>
        <h3>{{ title }}</h3>
        <table border="1">
            <thead>
                <tr>
                    <td>번호</td>
                    <td>제목</td>
                    <td>내용</td>
                </tr>
            </thead>
            <tbody>
                <tr v-for="tmp of state.faqdata" :key="tmp">
                    <td>{{tmp.faqno}}</td>
                    <td @click="handleContent" style="cursor: pointer;">{{tmp.title}}</td>
                    <td>{{tmp.content}}</td>
                </tr>
            </tbody>
        </table>
    </div> -->
    <div class="q-pa-md" style="width: 1200px;">
    <q-table
      title="자주 묻는 질문"
      :rows="state.faqdata"
      :columns="columns"
      row-key="title">

      <template v-slot:header="props">
        <q-tr :props="props">
          <q-th auto-width />
          <q-th
            v-for="col in props.cols"
            :key="col.name"
            :props="props"
          >
            <!-- {{ col.label }} -->
          </q-th>
        </q-tr>
      </template>

      <template v-slot:body="props">
        <q-tr :props="props">
          <q-td auto-width>
            <q-btn size="sm" color="accent" round dense @click="props.expand = !props.expand" :icon="props.expand ? 'remove' : 'add'" />
          </q-td>
          <q-td  @click="props.expand = !props.expand" :icon="props.expand ? 'remove' : 'add'" style="cursor: pointer;"
            v-for="col in props.cols"
            :key="col.name"
            :props="props"
          >
            {{ col.value }}
          </q-td>
        </q-tr>
        <q-tr v-show="props.expand" :props="props">
          <q-td colspan="100%">
            <div class="text-left" >{{props.row.content}}</div>
          </q-td>
        </q-tr>
      </template>

    </q-table>
  </div>
</template>

<script>
import { reactive } from '@vue/reactivity';
import { onMounted } from '@vue/runtime-core';
import axios from 'axios';
export default {
    props : {
        title : String
    },
    setup (props, {emit}) {

        const state = reactive({
            title : props.title,
            faqdata : []
        });

        const columns = [
        {
            name: 'name',
            required: true,
            label: '제목',
            align: 'left',
            field: faqdata => faqdata.title,
            format: val => `${val}`,
            sortable: true
        },
        ]

        const handleData = async() => {
            const url = `/COCO/api/faq/select.json`;
            const headers = {"Content-Type"  : "application/json"}
            const { data } = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200){
                state.faqdata = data.faq;
            }
        };

        onMounted(() => {
            handleData();
        });

        const handleInsert = async () => {
            const url = `/COCO/api/faq/insert.json`;
            const headers = {"Content-Type":"application/json"};
            const body = {title : state.faqdata.title, content : state.faqdata.content};
            const {data} = await axios.post(url, body, {headers});
            console.log(data);
            if(data.status === 200){
                alert('입력 완료');
                handleData();
            }
        };

        const handleEmit = () => {
            // 부모쪽으로 이벤트 발생
            emit('sendData',{key:'a', value:'b'});
        }

        return {state, handleEmit, handleInsert, columns};
    }
}
</script>

