<template>
    <div>
        <h4>대화내역</h4>
        {{state}}
        내용 : <textarea rows="6" v-model="state.content" /><br />
        <button @click="handleInsert">등록</button>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import axios from 'axios';
import { useRoute} from 'vue-router';
export default {
    setup () {
        const route = useRoute();
        const state = reactive({
            receiver : route.query.receiver,
            token : sessionStorage.getItem("TOKEN"),
            content : ''
        });

        const handleInsert = async () => { // 입력
            const url = `/COCO/api/chat/insert.json?receiver=${state.receiver}`;
            const headers = {"Content-Type":"application/json", "TOKEN" : state.token};
            const body = {content : state.content};
            const {data} = await axios.post(url, body, {headers});
            console.log(data);
            if(data.status === 200){
                alert('입력 완료!');
            }
        };

        return {state, handleInsert};
    }
}
</script>

