<template>
    <div class="q-pa-md row items-start q-gutter-md">
        <q-card class="my-card" flat>
            <q-card-section>
            <div class="text-h4 text-secondary" style="margin-top: 50px; margin-left: 300px;">마이페이지</div>
        </q-card-section>
        <br />
        
        <div class="q-pa-md" style="margin-top:-20px; margin-left: 300px;">
            <q-btn-group push>
                <q-btn style="background: #74b59d; color: white" push label="회원정보 변경" @click="handleMenu1()" />
                <q-btn style="background: #74b59d; color: white" push label="비밀번호 변경" @click="handleMenu2()" />
                <q-btn style="background: #74b59d; color: white" push label="회원탈퇴" @click="handleMenu3()" />
            </q-btn-group>
        </div>

        <div v-if="state.menu===1" class="q-pa-md" style="margin-top:-20px; margin-left: 300px;">
            <q-input outlined v-model="state.rows.id" label="아이디" style="width: 700px;"/>
            <q-input outlined v-model="state.rows.name" label="이름" style="width: 700px;"/>
            <q-input outlined v-model="state.rows.phone" label="연락처" style="width: 700px;"/>
            <q-input outlined v-model="state.rows.email" label="이메일" style="width: 700px;"/>
            <q-input outlined v-model="state.rows.birthday" label="생년월일" style="width: 700px;">
                <template v-slot:append>
                    <q-icon name="event" @click="handleDate"/>
                </template>
            </q-input><br />
            <div v-if="!state.btn" class="q-pa-md" >
                <q-date style="position:absolute; left: 1060px; top:189px; height: 280px;" v-model="state.rows.birthday" landscape mask="YYYY-MM-DD" color="teal" />
        </div>

        <!-- 이미지 -->
        <div v-if="state.rows.img == null" style="padding:15px" >
            <div v-if="state.file == null" class="text-caption text-grey">등록된 이미지가 없습니다.</div>
            <div v-if="state.file !== null">
                <img :src="state.imageurl" style="width:230px; padding: .1em .75em;" />
            </div>
        </div>
        
        <div v-if="state.rows.img != null">
            <td v-if="state.file === null"><img :src="`/COCO/api/member/image?no=${state.rows.img}`" style="max-width:200px; height:200px"></td>
            <div v-if="state.file !== null">
                <img :src="state.imageurl" style="width:230px; padding: .1em .75em;" />
            </div>
        </div>
        
        <q-card flat class="input-card">
            <!-- <div v-if="state.file == null">
                <img :src="state.imageurl" style="width:170px; padding: .1em .75em;" />
            </div> -->
            <q-item>
                <q-item-section align="center">
                    <div class="file-box" style="display:inline-block;">
                        <label style="width: 90px; position:absolute; left: 55px; top:-5px;" for="input-file">이미지등록</label>
                        <input type="file" id="input-file" @change="handleImage($event)" style="display:none" />
                    </div>
                </q-item-section>
            </q-item>
        </q-card>
        <!-- 이미지 -->
        
        <q-btn-group push>
            <q-btn style="background: #74b59d; position: absolute; left: 320px; top: 740px; color: white" push label="수정" @click="handleUpdate" />
        </q-btn-group>
    </div>
    </q-card>
</div>
    <div class="q-pa-md row items-start q-gutter-md">
        <div v-if="state.menu===2" class="q-pa-md" style="margin-top:-36px; margin-left: 318px;">
            <q-input outlined type="password" v-model="state.pw" label="현재 비밀번호" style="width: 700px;"/>
            <q-input outlined type="password" v-model="state.pw1" label="변경 비밀번호" />
            <q-input outlined type="password" v-model="state.pw2" label="비밀번호 확인" /><br />
            <q-btn-group push>
                <q-btn style="background: #74b59d; color: white" push label="변경" @click="handleUpdatePw" />
            </q-btn-group>
        </div>
    </div>
    <div class="q-pa-md row items-start q-gutter-md">
        <div v-if="state.menu===3" class="q-pa-md" style="margin-top:-52px; margin-left: 318px;">
            <q-input outlined type="password" v-model="state.pw" label="비밀번호" style="width: 700px;" /><br />
            <q-btn-group push>
                <q-btn style="background: #74b59d; color: white" push label="회원탈퇴" @click="handleDeletePw" />
            </q-btn-group>
        </div>
    </div>
</template>

<script>
import { reactive, ref} from '@vue/reactivity'
import axios from 'axios';
import { onMounted } from '@vue/runtime-core';
import router from '@/routes';
import { useStore } from 'vuex';
// import { useRouter } from 'vue-router';
export default {
    setup () {
        // const router = useRouter();
        const store = useStore();
        const state = reactive({
            menu : 1,
            rows : [],
            btn : true,
            file : null,
            imageurl  : require('@/assets/images/no_image.png'),
            pw : '',
            pw1 : '',
            pw2 : '',
            token : sessionStorage.getItem("TOKEN")
        })

        const handleData = async() => {
        const url = `/COCO/api/member/selectone.json`;
        const headers = {
            "Content-Type":"application/json",
            "TOKEN" : state.token
        };
            const { data } = await axios.get(url, {headers});
            if(data.status === 200) {
                console.log(data);
                state.rows = data.result;
            }
        };

        const handleDate = async () => {
            state.btn = false;
        };

        const handleUpdate = async () => {
            const url = `/COCO/api/member/update.json`;
            const headers = {"Content-Type":"application/json", "TOKEN" : state.token};
            const body = {name : state.rows.name, phone : state.rows.phone, birthday : state.rows.birthday, email : state.rows.email};
            const {data} = await axios.put(url, body, {headers});
            console.log(data);
            if(data.status === 200){
                alert('수정 완료');
                handleInsertImage();
                handleData();
                state.btn = true;
                router.push({path : '/Myinfo'});
            }
        };

        const handleInsertImage = async() => {
                const url = `/COCO/api/memberimg/insert.json`;
                const headers = {
                    "Content-Type":"multipart/form-data",
                    "TOKEN" : state.token
                };
                let body = new FormData();
                body.append("file", state.file);
                const {data} = await axios.post(url, body, {headers});
                console.log(data);
                // state.imageurl = require('@/assets/images/no_image.png');
        };

        const handleImage = (e) => {
            console.log(e.target.files[0]);
            console.log(e.target.files.length);
            if(e.target.files.length > 0){
                state.file = e.target.files[0];
                state.imageurl  = URL.createObjectURL( e.target.files[0] );
            }
            else {
                state.file = '';
                state.imageurl = require('@/assets/images/no_image.png');
            }
        };

        const handleUpdatePw = async () => {
            if(state.pw1 != state.pw2){
                alert('변경할 비밀번호와 확인이 서로 다릅니다!');
            }
            else{
                const url = `/COCO/api/member/updatepw.json`;
                const headers = {"Content-Type":"application/json", "TOKEN" : state.token};
                const body = {pw : state.pw, updatepw : state.pw1};
                const {data} = await axios.put(url, body, {headers});
                console.log(data);
                if(data.result === -1){
                    alert('현재 비밀번호가 다릅니다!');
                }
                else if(data.status === 200){
                    alert('비밀번호 변경 완료!');
                    router.push({path : '/Myinfo'});
                }
            }
        };

        const handleDeletePw = async () => {
            if(confirm('탈퇴하시겠습니까?')){
                if(state.pw === null){
                    alert('비밀번호를 입력해주세요!');
                }
                else{
                    const url = `/COCO/api/member/delete.json`;
                    const headers = {"Content-Type":"application/json", "TOKEN" : state.token};
                    const {data} = await axios.post(url, {}, {headers});
                    console.log(data);
                    if(data.status === 200){
                        alert('회원 탈퇴 완료!');
                        sessionStorage.removeItem("TOKEN");
                        store.commit('setLogged', false);
                        router.push({path : '/'});
                    }
                }
            }
        };

        onMounted(() => {
            handleData();
        })

        const handleMenu1 = () => {
            state.menu = 1;
        };

        const handleMenu2 = () => {
            state.menu = 2;
        };

        const handleMenu3 = () => {
            state.menu = 3;
        };

        return {state, handleDate, handleUpdate, handleInsertImage, 
            handleImage, files: ref(null), handleMenu1, handleMenu2,
            handleUpdatePw, handleMenu3, handleDeletePw};
    }
}
</script>

<style lang="css" scoped>
</style>