<template>
    <div>
        <div class="q-pa-md row items-start q-gutter-md">
        <q-card class="my-card" flat>
            <q-card-section>
            <div class="text-h4 text-secondary">상세내용</div>
        </q-card-section>
        <br />

        <div class="q-pa-md" style="margin-top:-20px;">
            <q-input outlined v-model="state.qnadata.title" label="제목" style="width: 689px;" />
            <q-select outlined v-model="state.qnadata.type" :options="options" label="유형" style="width: 689px;"/>
            <q-input outlined type="textarea" color="secondary" v-model="state.qnadata.content" label="내용"
            style="height: 340px, width : 689px, margin-top:-20px;" counter maxlength="1000" /><br />
            <q-btn-group push>
                <q-btn style="background: #74b59d; color: white" push label="뒤로가기" @click="handleReturn()" />
                <q-btn style="background: #74b59d; color: white" push label="수정" @click="handleInsert()" />
                <q-btn style="background: #74b59d; color: white" push label="삭제" @click="handleDelete()" />
                <q-btn style="background: #74b59d; color: white" push label="답변" @click="handleReply()" />
            </q-btn-group>
        </div>
        </q-card>

    </div>
    <div class="q-pa-md">
        <q-table title="답변" :rows="state.replydata" :columns="columns" row-key="replyno" style="margin-left: 20px; width: 900px;" />
    </div>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import { useRoute, useRouter } from 'vue-router';
import axios from 'axios';
import { onMounted } from '@vue/runtime-core';
export default {
    setup () {
        const columns = [
            // { name: 'qnano', label: '번호', align: 'left', field: qnadate => qnadate.qnano, format: val => `${val}`, sortable: true },
            { name: 'content', required: true, label: '답변', align: 'left', field: replydata => replydata.content, format: val => `${val}`, sortable: true },
            { name: 'regdate', label: '작성일', align: 'left', field: replydata => replydata.regdate, sortable: true },
        ]
        const route = useRoute();
        const router = useRouter();
        const state = reactive({
            qnano : Number(route.query.qnano),
            qnadata : '',
            replydata : [],
            token : sessionStorage.getItem("TOKEN")
        });

        onMounted(() => {
            handleData();
            handleDataReply();
        });

        const handleData = async () => {
            const url = `/COCO/api/qna/selectone.json?qnano=${state.qnano}`;
            const headers = {"Content-Type":"application/json"};
            const {data} = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200){
                state.qnadata = data.qna;
            }
        };

        const handleDataReply = async () => {
            const url = `/COCO/api/reply/selectlist.json?qnano=${state.qnano}`;
            const headers = {"Content-Type":"application/json"};
            const {data} = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200){
                state.replydata = data.reply;
            }
        };

        const handleInsert = async () => {
            if(confirm('수정 하시겠습니까?')){
                const url = `/COCO/api/qna/update.json?qnano=${state.qnano}`;
                const headers = {"Content-Type":"application/json", "token" : state.token};
                const body = {title : state.qnadata.title, type : state.qnadata.type, content : state.qnadata.content};
                const {data} = await axios.post(url, body, {headers});
                console.log(data);
                if(data.status === 200){
                    alert('수정 완료!');
                    handleData();
                }
            }
        };

        const handleDelete = async () => {
            if(confirm('삭제 하시겠습니까?')){
                const url = `/COCO/api/qna/delete.json?qnano=${state.qnano}`;
                const headers = {"Content-Type":"application/json", "token" : state.token};
                const {data} = await axios.post(url, {}, {headers});
                console.log(data);
                if(data.status === 200){
                    alert('삭제 완료!');
                    handleData();
                    router.push({path : '/contact'});
                }
            }
        };

        const handleReply = async () => {
            router.push({path:'/reply', query : {qnano : state.qnano}});
        };

        const handleReturn = async () => {
            router.push({path : '/contact'});
        };

        return {state, handleReturn, handleInsert, handleReply, handleDelete, options: ['결제', '강좌', '강사', '계정'], columns};
    }
}
</script>

