<template>
    <div class="q-pa-md row">
        <div class="col-4"></div>

        <div class="col-4">
            <br /><br /><br />
            <div>
                <q-input dense type="text" label-color="grey" label="받는 사람" borderless color="secondary" v-model="state.receiver" input-class="text-body2" />
            </div>

            <q-card>
                <q-input borderless autogrow type="textarea" color="secondary"
                    v-model="state.content" style="height: 300px; margin: 0 15px;" counter maxlength="500" />
            </q-card>

            <div class="row" style="margin-top:10px;">
                <div class="text-grey text-body2">{{state.coursTitle}}</div>
                <q-space />
                <q-btn dense flat @click="handleCancer" color="negative" label="취소" style="margin-right:10px; width:60px;" />
                <q-btn dense color="primary" @click="handleInsert" label="보내기" style="width:60px;" />
            </div>
        </div>

        <div class="col-4"></div>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import axios from 'axios';
export default {
    setup () {
        const state = reactive({
            receiver : '',
            token : sessionStorage.getItem("TOKEN"),
            content : '',
            chat : [],
        });

        const handleInsert = async () => { // 입력
            if(state.receiver === '') {
                alert('수신자 아이디를 입력하세요.')
                return false;
            }
            if(state.content === '') {
                alert('내용을 입력하세요.');
                return false;
            }
            const url = `/COCO/api/chat/insert.json?receiver=${state.receiver}`;
            const headers = {
                "Content-Type":"application/json",
                "TOKEN" : state.token
            };
            const body = {
                content : state.content
            };
            const {data} = await axios.post(url, body, {headers});
            console.log(data);
            if(data.status === 200){
                alert(state.receiver + '님에게 메시지를 보냈습니다.');
                history.back();
            }
        };

        const handleCancer = () => {
            history.back();
        }

        return {
            state,
            handleInsert,
            handleCancer
        };
    }
}
</script>

