<template>
    <div class="q-pa-md row">
        <div class="col-3"></div>

        <div class="col-6">
            <br />
            <div class="row">
                <q-input type="text" borderless color="secondary" v-model="state.title" :maxlength="30"
                    placeholder="제목을 입력하세요" input-class="text-h6" style="width:85%" />
                    <q-space />
                <q-checkbox size="xs" left-label color="secondary" v-model="state.secret" :true-value=1 :false-value=0>
                    <q-icon name="lock" />
                </q-checkbox>
            </div>

            <q-card>
                <q-input borderless autogrow type="textarea" color="secondary"
                    v-model="state.content" style="height: 500px;margin: 0 15px;" counter maxlength="500" />
            </q-card>

            <div class="row" style="margin-top:10px;">
                <div class="text-grey text-body2">{{state.coursTitle}}</div>
                <q-space />
                <q-btn @click="handleCancer" label="취소" style="margin-right:10px;" />
                <q-btn color="secondary" @click="handleUpdate" label="수정" />
            </div>
        </div>

        <div class="col-3"></div>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import { useRoute, useRouter } from 'vue-router'
import axios from 'axios';
import { onMounted } from '@vue/runtime-core';
export default {
    setup() {
        const route = useRoute();
        const router = useRouter();
        const state = reactive({
            token : sessionStorage.getItem("TOKEN"),
            commentno : route.query.commentno,
            title : '',
            content : '',
            secret : 0,
            coursTitle : '',
            commentOne : [],
            userId : '',
        })

        const handleCommentData = async() => {
            const url = `/COCO/api/comment/selectonecom.json?commentno=${state.commentno}`;
            const headers = {
                "Content-Type":"application/json"
            };
            const { data } = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200) {
                state.commentOne = data.comment;
                state.commentno = data.comment.commentno;
                state.title = data.comment.title;
                state.secret = data.comment.secret;
                state.content = data.comment.content;
                state.userId = data.userid;
                state.coursTitle = data.courstitle;
            }
        }

        const handleUpdate = async() => {
            if(state.title === '') {
                alert('제목을 입력하세요');
                return false;
            }
            if(state.content === '') {
                alert('내용을 입력하세요');
                return false;
            }
            const url = `/COCO/api/comment/update.json`;
            const headers = {
                "Content-Type":"application/json"
            };
            const body = {
                commentno : state.commentno,
                title : state.title,
                content : state.content,
                secret : state.secret
            }
            const { data } = await axios.put(url, body, {headers});
            console.log(data);
            if(data.status === 200) {
                alert('수정되었습니다.');
                router.push({path : '/commentone', query : {commentno : state.commentno}})
            }
        }


        const handleCancer = () => {
            history.back();
        }

        onMounted(() => {
            handleCommentData();
        })

        return {
            state,
            handleCancer,
            handleUpdate
        }
    }
}
</script>

