<template>

<div align="center"  class="back-back"></div>

<div class="join-box">
  <q-card class="join-card" style="opacity:0.9">
    <q-card-section horizontal>
      <q-img src="@/assets/images/bg/piano_stair.jpg" width="50%" />

        <div style="width:540px;">

          <q-card-section align="right">
            <q-img src="@/assets/images/logo/4Dlogo.png" 
              width="80px" />
          </q-card-section>

          <q-card-section align="center">
            <div class="text-overline"
              style=" margin-top: -20px; font-size:24px; color:#4d9056;">
                SIGN UP
              </div>
          </q-card-section>

            <!-- <q-form @submit="handleJoin"> -->
            <q-card-section align="center">

            <div align="left" class="text-overline input-label">아이디</div>
                <q-input autofocus outlined dense color="secondary" class="login_card_item"
                    :ref="idx => {form[0] = idx}" v-model="state.id" @keyup="handleIDCheck"
                    hint="아이디는 5~13자 사이여야 합니다." counter :maxlength="13" placeholder="아이디를 입력하세요" >
                    <template v-slot:append>
                        <label style="font-size:small" v-html="state.idcheck"></label>
                    </template>
                </q-input>

            <div align="left" class="text-overline input-label">비밀번호</div>
                <q-input outlined dense color="secondary" :type="isPwd ? 'password' : 'text'" :ref="idx => {form[1] = idx}"
                    class="login_card_item" v-model="state.pw" :maxlength="13" placeholder="비밀번호를 입력하세요">
                    <template v-slot:append>
                        <q-icon
                        :name="isPwd ? 'visibility_off' : 'visibility'"
                        class="cursor-pointer" @click="isPwd = !isPwd" />
                    </template>
                </q-input>

                <q-input outlined dense class="login_card_item" color="secondary" hint="비밀번호는 6~13자 사이여야 합니다."
                    :ref="idx => {form[2] = idx}" v-model="state.pw1" style="margin-top:5px;"
                    :type="isPwd ? 'password' : 'text'" :maxlength="13" placeholder="비밀번호 확인">
                </q-input>

            <div align="left" class="text-overline" style="width:77%;margin-top:30px;">이름</div>
                <q-input outlined dense class="login_card_item" color="secondary" :ref="idx => {form[3] = idx}"
                    v-model="state.name" counter :maxlength="20" placeholder="이름을 입력하세요" />

            <div align="left" class="text-overline input-label">이메일</div>        
                <q-input class="login_card_item" outlined dense color="secondary" :ref="idx => {form[4] = idx}"
                    v-model="state.email" counter :maxlength="50" placeholder="ex) coconut@gmail.com">
                </q-input>

            </q-card-section>

            <q-card-actions style="margin-top:30px;margin-bottom: 30px;" align="center">

              <q-btn color="secondary" @click="handleJoin"
                class="login_card_item" label="회원가입" />

            </q-card-actions>
            <!-- </q-form> -->

        </div>
    </q-card-section>
</q-card>


</div>

</template>

<script>
import { reactive, ref, toRefs } from '@vue/reactivity';
import { useRouter } from 'vue-router';
import axios from 'axios';
export default {
    setup() {
        const router = useRouter();

        const form = ref([]);

        const state = reactive({
            id      : '',
            pw      : '',
            pw1     : '',
            name    : '',
            phone   : '',
            email  : '',
            idcheck : '<font style="font-size:smaller">중복확인</font>'
        });

        const handleJoin = async () => {
            if(state.id === '') {
                alert('아이디를 입력하세요.');
                form.value[0].focus();
                return false;
            }
            if(state.id.length < 5) {
                alert('아이디는 5자 이상, 13자 이하여야 합니다.');
                form.value[0].focus();
                return false;
            }
            if(state.id.length > 13) {
                alert('아이디는 5자 이상, 13자 이하여야 합니다.');
                form.value[0].focus();
                return false;
            }
            if(state.pw === '') {
                alert('비밀번호를 입력하세요.');
                form.value[1].focus();
                return false;
            }
            if(state.pw.length < 6) {
                alert('비밀번호는 6자 이상이여야 합니다.');
                form.value[1].focus();
                return false;
            }
            if(state.pw !== state.pw1) {
                alert('비밀번호가 일치하지 않습니다.');
                form.value[2].focus();
                return false
            }
            if(state.name === '') {
                alert('이름을 입력하세요.');
                form.value[3].focus();
                return false;
            }
            if(state.email === '') {
                alert('이메일을 입력하세요.')
                form.value[4].focus();
                return false;
            }
            if(!state.email.includes('@')) {
                alert('이메일 형식이 올바르지 않습니다.');
                form.value[4].focus();
                return false
            }

            const url = `/COCO/api/member/join.json`;
            const headers = {"Content-Type":"application/json"};
            const body = {
                id : state.id,
                pw : state.pw,
                name : state.name,
                email : state.email
            };

            const {data} = await axios.post(url, body, {headers});
            console.log(data);
            if(data.status === 200) {
                alert('회원가입이 완료되었습니다.');
                router.push({path:'/login'})
            }
        }

        const handleIDCheck = async() => {
            if(state.id.length > 4) {
                const url = `/COCO/api/member/idcheck.json?id=${state.id}`
                const headers = {"Content-Type":"application/json"}
                const { data } = await axios.get(url, {headers});
                console.log(data);
                if(data.status === 200) {
                    if(data.result === true) {
                        state.idcheck = '<font color="red" style="font-size:smaller">사용불가</font>';
                    }
                    else {
                        state.idcheck = '<font color="green" style="font-size:smaller">사용가능</font>';
                    }
                }
            }
        }

        return {
            form, 
            state,
            isPwd:ref(true),
            handleJoin,
            handleIDCheck,
            ...toRefs(state),
            emailOption : ['gmail.com', 'naver.com', 'daum.net']
        }
    }
}
</script>

<style lang="css" scoped src="@/assets/css/Sign.css">

</style>