<template>
    <div v-if="!state.yesReview" class="q-pa-md flex flex-center text-body1">
            작성한 리뷰가 없습니다.
    </div>

    <div class="q-pa-md row">

        
        <div v-if="state.yesReview" >
            <div v-for="tmp in state.reviewList" :key="tmp" style="display:inline-block;">
                <q-card style="margin:30px; width:400px;">
    
                    <q-card-section style="padding:0;margin:0" class="title" side @click="handleSelectOneCours(tmp.coursno)">
                        <q-img :src="`/COCO/api/cours/image?no=${tmp.imageno}`" fit="fill" :ratio="4/1" style="width:100%;">

                            <div class="absolute-full text-subtitle2">
                                <q-item-label class="text-bold text-white text-body1">
                                    {{tmp.title}}
                                </q-item-label>

                                <div class="row">
                                    <q-rating v-model="tmp.rating" size="1.5em" no-dimming icon="star_border"
                                    icon-selected="star" readonly  />
                                    <span style="margin-top:2px;"> &nbsp;({{tmp.rating}})</span> 
                                </div>
                            </div>

                        </q-img>
    
                    </q-card-section>
    
                    <q-separator />
    
                    <q-card-section style="height:80px;">
                        <q-item-label>{{tmp.content}}</q-item-label>
                    </q-card-section>
                    <q-card-actions align="right">
                        <q-item-label class="text-grey">{{moment(tmp.regdate).format("yyyy-MM-DD")}}</q-item-label>
                    </q-card-actions>
                </q-card>
            </div>
        </div>

        
        
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import { onMounted } from '@vue/runtime-core'
import axios from 'axios'
import { useRouter } from 'vue-router'
export default {
    setup () {
        const moment = require('moment');
        const router = useRouter();
        const state = reactive({
            token : sessionStorage.getItem("TOKEN"),
            yesReview : true,
            reviewList : []
        })

        const handleReviewData = async() => {
            const url = `/COCO/api/review/selectmem.json`;
            const headers = {
                "Content-Type":"application/json",
                "TOKEN" : state.token
            }
            const { data } = await axios.get(url, {headers});
            console.log(data)
            if(data.status === 200) {
                data.review.sort(function(a, b) {
                    return b.reviewno - a.reviewno
                });
                state.reviewList = data.review;
                state.yesReview = true;
            }
            else {
                state.yesReview = false;
            }
        }

        const handleSelectOneCours=(obj) => {
            router.push({path: '/coursone', query:{coursno : obj}})
        }

        onMounted(() => {
            handleReviewData();
        })

        return {
            state,
            moment,
            handleSelectOneCours
        }
    }

}
</script>

<style lang="css" scoped>
.title:hover {
    cursor: pointer;
    /* background: #6A7698; */
}
</style>