                        <q-card class="card" @click="handleJoin('MIDI')">
                            <img src="@/assets/images/reference/MIDI.jpg">
                            <q-item>
                                <q-item-section>
                                    <q-item-label>MIDI</q-item-label>
                                    <q-item-label caption></q-item-label>
                                </q-item-section>
                            </q-item>
                        </q-card>
                    </div>
                </div>

            </div>

            <div v-if="state.tchr">

                <div class="q-pa-md row">
                    <div class="q-ma-md text-h6 ">카테고리 선택</div>

                    <div class="row">
                        <q-card class="card" @click="handleUpdate('보컬')">
                            <img src="@/assets/images/reference/vocal.jpg">
                            <q-item>
                                <q-item-section>
                                    <q-item-label>보컬</q-item-label>
                                    <q-item-label caption></q-item-label>
                                </q-item-section>
                            </q-item>
                        </q-card>

                        <q-card class="card" @click="handleUpdate('기타')">
                            <img src="@/assets/images/reference/guitar.jpg">
                            <q-item>
                                <q-item-section>
                                    <q-item-label>기타</q-item-label>
                                    <q-item-label caption></q-item-label>
                                </q-item-section>
                            </q-item>
                        </q-card>

                        <q-card class="card" @click="handleUpdate('건반')">
                            <img src="@/assets/images/reference/keyboard.jpg">
                            <q-item>
                                <q-item-section>
                                    <q-item-label>건반</q-item-label>
                                    <q-item-label caption></q-item-label>
                                </q-item-section>
                            </q-item>
                        </q-card>

                        <q-card class="card" @click="handleUpdate('드럼')">
                            <img src="@/assets/images/reference/drums.jpg">
                            <q-item>
                                <q-item-section>
