            </div>
            

        </div>

        <div class="col-1"></div>
            <!-- <button @click="handleJoin()">등록신청</button> -->
    </div>

    <!-- 한번 신청한 이후에는 곳으로 옴 -->
    
</template>

<script>
import { reactive } from '@vue/reactivity';
import { useRouter } from 'vue-router'
import axios from 'axios';
import { onMounted } from '@vue/runtime-core';
export default {
    setup() {
        const router = useRouter();
        const state = reactive({
            category : '',
            intro : '',
            token : sessionStorage.getItem("TOKEN"),
            tchr : false
        })

        const handleData = async() => {
            const url = `/COCO/api/teacher/selectOneteacher.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            }
            const { data } = await axios.get(url, {headers});
            if(data.status === 200) {
                state.tchr = true;
                state.category = data.result.category;
                state.intro = data.result.intro;
            }
        }

        const handleUpdate = async(inst) => {
            const url = `/COCO/api/teacher/updateteacher.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            };
            const body = {
                category : inst,
                intro : state.intro
            }
            const { data } = await axios.post(url, body, {headers});
            if(data.status === 200) {
                router.push({path:'/tchrinfo', query:{cate : inst}});
            }
        }

        const handleJoin = async(inst) => {
            const url = `/COCO/api/solda/jointest.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            };
            const body = {
                category : inst,
                // steps : 1
            }
            const { data } = await axios.post(url, body, {headers});
            console.log("solda.teacher => ", data);
            if(data.status === 200) {
                router.push({path:'/tchrinfo', query:{cate : inst}});
            }
        }

        onMounted(() => {
            handleData();
        })


        return {state, handleJoin, handleUpdate}
    }
}
</script>

<style lang="css" scoped>

.card{
    width: 100%;
    max-width: 250px;
    margin: 20px;
    cursor: pointer;
}

</style>