  <div style="margin-top:0px;">

    <div>
      <q-carousel v-model="slide" transition-prev="slide-right" class="main-carousel"
          transition-next="slide-left" arrows animated infinite
          @mouseenter="autoplay = false" height="500px" @mouseleave="autoplay = true" :autoplay="autoplay">
    
        <q-carousel-slide class="slide" :name="1" img-src="@/assets/images/bg/asing.jpg">

          <div v-if="!state.logged" class="join">
