                <q-card class="recours-card" align="center" @click="handleSelectCours(state.mdCoursno)">
                  <img :src="`/COCO/api/cours/image?no=${state.mdCoursImgno}`" class="recours-img" />
                </q-card>
              </div>
    
        </q-carousel-slide>
    
        <q-carousel-slide class="slide" :name="3" img-src="@/assets/images/bg/garage.jpg">
    
          <div class="join">
            <h6 class="text-white">IMPROVE YOUR SKILS,</h6>
            <div class="text-white text-h6" style="margin-top:-50px;">PROVE YOUR INSPIRE</div>
            <!-- <q-btn class="text-white bg-deep-orange" icon="keyboard_arrow_right" label="강사신청" @click="handleTeacher" /> -->
          </div>

        </q-carousel-slide>
    
      </q-carousel>

    </div>
  

    
    
    <div class="row top-section">

      <div class="col-2"></div>
      
      <!-- 메인 컨텐츠 -->
      <div class="col-8">
        
        <br />
        <div>
          <br />
          <div class="text-h6" style="margin-bottom:-20px;">인기 TOP 7</div>
            <q-btn class="top-left" flat padding="sm" square fab icon="chevron_left" @click="scrollTop(-300)" :ripple="false" />
            <q-btn class="top-right" flat padding="sm" square fab icon="chevron_right" @click="scrollTop(300)" :ripple="false" />

          <q-scroll-area :thumb-style="{opacity:0}" ref="topPosition" style="height: 405px;" @scroll="topScroll">
            
            <div class="row no-wrap">
              <div once transition="scale" v-for="(tmp, idx) in state.topData" :key="idx" :name="tmp.coursno" class="q-pa-sm">
                <q-card flat class="main-cours-card" align="center" @click="handleSelectCours(tmp.coursno)">          
                  <q-card-section class="main-cours-img" align="center">
                      <img :src="`/COCO/api/cours/image?no=${tmp.imageno}`" class="main-img-src" :ratio="4/3" />
                  </q-card-section>
  
                  <div align="left" class="main-cours-info">
                          
                    <div class="text-h6 hov title" style="height: 45px; line-height: 120%;">{{tmp.title}}</div>
                    
                    <div class="hov tchr text-overline">{{tmp.teachername}}</div>
                    <div class="row">
                        <q-rating class="rating-box" v-model="tmp.ratingavg" size="1.5em" no-dimming icon="star_border" icon-selected="star" readonly  /> 
                        <span class="hov" v-if="tmp.reviewtotal !=0">({{tmp.ratingavg}})</span>
                    </div>
                    
                    <div class="price text-weight-bold hov">₩{{tmp.price}}</div>
                
                  </div> 
                </q-card>
              </div>

            </div>          
          </q-scroll-area>
        </div>



</div>
<div class="col-2"></div>

</div>

<div class="baron-section">
  
</div>

<div class="row middle-section">
  
  <div class="col-2"></div>

  <div class="col-8">

    <!-- <hr class="hr-line" /> -->
    <br />
    <div>
      <div class="text-h6" style="margin-bottom:-20px;">최신 강좌</div>
        <q-btn class="top-left" flat padding="sm" square fab icon="chevron_left" @click="scrollNew(-300)" :ripple="false" />
        <q-btn class="top-right" flat padding="sm" square fab icon="chevron_right" @click="scrollNew(300)" :ripple="false" />
  
      <q-scroll-area :thumb-style="{opacity:0}" ref="newPosition" style="height: 405px;" @scroll="newScroll">
      
        <div class="row no-wrap">
          <div once transition="scale" v-for="(tmp, idx) in state.newData" :key="idx" :name="tmp.coursno" class="q-pa-sm">
            <q-card flat class="main-cours-card" align="center" @click="handleSelectCours(tmp.coursno)">
              <q-card-section class="main-cours-img" align="center">
                  <img :src="`/COCO/api/cours/image?no=${tmp.imageno}`" class="main-img-src" :ratio="4/3" />
              </q-card-section>
  
              <div align="left" class="main-cours-info">
                      
                <div class="text-h6 hov title" style="height: 45px; line-height: 120%;">{{tmp.title}}</div>
                
                <div class="hov tchr text-overline">{{tmp.teachername}}</div>
                <div class="row">
                    <q-rating class="rating-box" v-model="tmp.ratingavg" size="1.5em" no-dimming icon="star_border" icon-selected="star" readonly  /> 
                    <span class="hov" v-if="tmp.reviewtotal !=0">({{tmp.ratingavg}})</span>
                </div>
                
                <div class="price text-weight-bold text-subtitle1 hov">₩{{tmp.price}}</div>
  
              </div>
            </q-card>
          </div>
        </div>
      
      </q-scroll-area>
    </div>


  </div>

  <div class="col-2"></div>

</div>

<div class="jungle-section">

</div>

<div class="row bottom-section" >

<div class="col-2"></div>

<div class="col-8">


  <!-- <hr class="hr-line" /> -->
  <br /><br />
  
  <div class="row">


    <div style="width:50%;">
      <div class="text-h6">최신 리뷰</div>
  
      <q-scroll-area :thumb-style="{opacity:0}" style="height: 300px;">
      
        <div>
          <div once transition="scale" v-for="(tmp, idx) in state.reviewList" :key="idx" :name="tmp.reviewno" class="q-pa-sm">
  
            <q-card style="margin:15px 0;width: 90%;">
  
              <q-card-section class="row" style="padding:0;margin:0">
  
                <q-img :src="`/COCO/api/cours/image?no=${tmp.coursimageno}`" fit="fill" :ratio="4/1" style="width:100%">
                  <div class="absolute-full">
                    
                    <q-item-section style="margin-top:-5px;">
                      <q-item-label class="text-bold text-white text-body1">{{tmp.userid}}</q-item-label>
                      <div class="row" style="margin-left:-2px; margin-top:3px;">
                        <q-rating v-model="tmp.rating" size="1.5em" no-dimming icon="star_border" icon-selected="star" readonly  /> 
                        <span style="margin-top:2px;"> &nbsp;({{tmp.rating}})</span> 
                      </div>
                    </q-item-section>
                  </div>
                
                </q-img>
  
              </q-card-section>
  
              <q-separator />
  
              <q-card-section style="min-height:100px;">
                  <q-item-label>{{tmp.content}}</q-item-label>
                  <br />
                  <q-item-label class="text-grey review-courstitle" @click="handleSelectCours(tmp.coursno)"> {{tmp.title}}</q-item-label>
                </q-card-section>
                
              </q-card>
          </div>
        </div>
      
      </q-scroll-area>
    </div>  
  
    <div style="width:50%">
      <div class="text-h6">커뮤니티 인기글</div>
      <br />
      <q-markup-table flat>
        <thead>
          <tr style="height:20px;" class="bg-secondary text-white text-body1">
            <th class="text-left head-text">번호</th>
            <th class="text-center head-text">제목</th>
            <th class="text-right head-text">작성자</th>
            <th class="text-right head-text">좋아요</th>
            <th class="text-right head-text">작성일</th>
          </tr>
        </thead>
        <tbody>
          <tr class="board-row" v-for="(tmp, idx) in state.boardLikeList.slice(0,5)" :key="idx" @click="handleBoardOne(tmp.no)">
            <td width="10%" class="text-left">{{tmp.no}}</td>
            <td width="60%" class="text-left">{{tmp.title}}</td>
            <td width="10%" class="text-right">{{tmp.nickname}}</td>
            <td width="10%" class="text-right">{{tmp.boardlike}}</td>
            <td width="10%" class="text-right">{{ moment(tmp.regdate).format("MM-DD")}}</td>
          </tr>
        </tbody>
      </q-markup-table>
    </div>  
  
  
  
  </div>
  
  
  </div>

</div>

<div class="col-2"></div>

</div>
<br /><br /><br />
</template>

<script>
import { reactive, ref, toRefs } from '@vue/reactivity';
import { computed, onMounted } from '@vue/runtime-core';
import { useStore } from 'vuex';
import axios from 'axios';
import { useQuasar } from 'quasar'
import { useRouter } from 'vue-router';
export default {
  setup() {
    const store = useStore();
    const router = useRouter();
    const moment = require('moment');
    const state = reactive({
      token : sessionStorage.getItem("TOKEN"),
      logged : computed(() => store.getters.getLogged),
      teacherRole : computed(() => store.getters.getTeacherRole),
      appToken : computed(() => store.getters.getAppToken),
      text : '',
      mainGnb : computed(() => store.getters.getMainGnb),
      topData : [],
      newData : [],
      reviewList : [],
      avatar : require('@/assets/images/logo/coconut_500.png'),
      boardLikeList : [],
      mdCoursno : 34,
      mdCoursData : [],
      mdCoursImgno : 0
    });

    const handleSelectCours = (obj) => {
      router.push({path:'/coursone', query:{coursno : obj}})
    }

    const handleTopData = async () => {
      const url = `/COCO/api/cours/findcours.json?page=1&opt=5&row=1`;
      const headers = {"Content-Type" : "application/json"};
      const { data } = await axios.get(url, {headers})
      if(data.status === 200) {
        let size = 7
        if(data.courslist.length < size) {
          size = data.courslist.length
          for(let i =0; i<size; i++) {
            state.topData[i] = data.courslist[i]
          }
        }
        else {
          for(let i=0; i<7 ; i++) {
            state.topData[i] = data.courslist[i]
          }
        }
      }
    }

    const handleNewData = async() => {
      const url = `/COCO/api/cours/findcours.json?page=1`
      const headers = {"Content-Type" : "application/json"};
      const { data } = await axios.get(url, {headers})
      if(data.status === 200) {
        let size = 7
        if(data.courslist.length < size) {
          size = data.courslist.length
          for(let i =0; i<size; i++) {
            state.newData[i] = data.courslist[i]
          }
        }
        else {
          for(let i=0; i<7 ; i++) {
            state.newData[i] = data.courslist[i]
          }
        }
      }
    }

    const handleReviewData = async() => {
      const url = `/COCO/api/solda/selectreview.json`;
      const headers = {"Content-Type" : "application/json"};
      const { data } = await axios.get(url, {headers})
      console.log(data)
      if(data.status === 200) {
        data.review.sort(function(a, b) {
          return b.reviewno - a.reviewno;
        });
        let size = 5
        if(data.review.length < size) {
          size = data.review.length
          for(let i = 0; i<size; i++) {
            state.reviewList[i] = data.review[i];
          }
        }
        else {
          for(let i=0; i<5; i++) {
            state.reviewList[i] = data.review[i];
          }
        }
      }
    }

    const handleCommunityData = async() => {
      const url = `/COCO/api/board/selectlist?like=1`
      const headers = {
        "Content-Type" : "application/json"
      };
      const { data } = await axios.get(url, {headers});
      console.log(data.result.list);
      if(data.status === 200) {
        state.boardLikeList = data.result.list
        data.result.list.sort(function(a, b) {
          return b.boardlike - a.boardlike
        });
        let size = 5
        if(data.result.list.length < size) {
          size = data.result.list.length
          for(let i=0; i<size; i++) {
            state.boardLikeList[i] = data.result.list[i];
          }
        }
        else {
          for(let i = 0; i<5; i++) {
            state.boardLikeList[i] = data.result.list[i]
          }
        }
      }
    }

    const handleCoursOneData = async() => {
            const url = `/COCO/api/cours/selectone.json?coursno=${state.mdCoursno}`;
            const headers = {"Content-Type":"application/json"}
            
            const { data } = await axios.get(url, {headers:headers});
            console.log('강좌조회',data)
            state.mdCoursData = data.cours
            state.mdCoursImgno = data.coursimage
        }

    const handleBoardOne = (obj) => {
      router.push({path: '/boardone', query :{no : obj}})
    }

    const handleTopLeft = () => {
      document.getElementById("topscroll");
      Element.scrollBy(300, 0)
      // document.getElementById("topScroll").scrollBy(300, 0)
    }

    onMounted(() => {
      handleTopData();
      handleNewData();
      handleReviewData();
      handleCommunityData();
      handleCoursOneData();
      store.dispatch('handleTeacherRole');
      store.dispatch('handleTeacherData');
    });

    const handleSearch = () => {
      store.commit('setMainText', state.text)
      router.push({path:'/cours'})
    }

    const topPosition = ref(null);
    const newPosition = ref(null);
    const scrollInfo = ref({})
    const scrollInfoNew = ref({})
    const $q = useQuasar()
    
      return {
        state,
        moment,
        slide : ref(1),
        autoplay: ref(true),
        layout: computed(() => {return $q.screen.lt.sm ? 'dense' : ($q.screen.lt.md ? 'comfortable' : 'loose')}),
        handleSearch,
        ...toRefs(state),
        cours : ref(1),
        handleSelectCours,
        handleBoardOne,
        handleTopLeft,
        topPosition,
        newPosition,
        scrollInfo,
        scrollInfoNew,
        newScroll (info) {
          scrollInfoNew.value = info.horizontalPosition
        },
        scrollTop(val) {
          topPosition.value.setScrollPosition('horizontal', scrollInfo.value + val, 300)
        },
        scrollNew(val) {
          newPosition.value.setScrollPosition('horizontal', scrollInfoNew.value + val, 300)
        },
        topScroll (info) {
        scrollInfo.value = info.horizontalPosition
        }
      }
  }
}
</script>

<style lang="css" scoped>
@import "@/assets/css/MainPage.css";
@import "@/assets/css/CoursFor.css";
@import "@/assets/css/MainShape.css";
.cours-link:hover {
  cursor: pointer;
}
.review-courstitle:hover {
  cursor: pointer;
  text-decoration: underline;
}
.head-text {
    font-weight: bolder;
}
</style>