<template>
    <div v-if="!state.yesComment" class="q-pa-md flex flex-center text-body1">
        작성한 글이 없습니다.
    </div>

    <div>
        <div v-if="state.yesComment">
            <q-markup-table flat>
                <thead class="bg-secondary text-white">
                    <tr style="height:20px;">
                        <th class="text-left head-text">번호</th>
                        <th class="text-center head-text">제목</th>
                        <th class="text-right head-text">강좌명</th>
                        <th class="text-right head-text">작성일</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(tmp, idx) in state.commentList" :key="idx">
                        <td width="3%">{{tmp.commentno}}</td>

                        <td class="text-left my-comment-title" @click="handleSelectOneComment(tmp.commentno)">
                            {{tmp.title}}<span class="text-grey">({{tmp.totalrecomment}})</span>
                        </td>

                        <td class="text-right my-comment-coursname" width="15%" @click="handleSelectOneCours(tmp.coursno)">
                            {{tmp.coursname}}
                        </td>
                    
                        <td class="text-right" width="10%">{{moment(tmp.regdate).format('MM-DD HH:mm')}}</td>
                    </tr>
                </tbody>
            </q-markup-table>
            <div class="q-pa-lg flex flex-center" >
                <q-pagination color="secondary" v-model="state.page" :max-pages="5"
                    :boundary-numbers="false" :ellipses="false" @click="handlePage(state.page)"
                    :max="state.total"/>
            </div>
        </div>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import axios from 'axios'
import { onMounted } from '@vue/runtime-core'
import { useRouter } from 'vue-router'
export default {
    setup () {
        const moment = require('moment')
        const router = useRouter();
        const state = reactive({
            token : sessionStorage.getItem("TOKEN"),
            page : 1,
            total : 1,
            commentList : [],
            yesComment : true
        })

        const handleMyComment = async() => {
            const url = `/COCO/api/comment/selectmember.json?page=${state.page}`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            }
            const { data } = await axios.get(url, {headers});
            console.log(data)
            if(data.status === 200) {
                state.total = Math.ceil(data.total/10);
                state.commentList = data.list;
                state.yesComment = true;
            }
            else {
                state.yesComment = false;
            }
        }

        const handleSelectOneComment = (obj) => {
            router.push({path:'/commentone', query: {commentno : obj, coursno : 0}})
        }

        const handleSelectOneCours = (obj) => {
            router.push({path: '/coursone', query : {coursno : obj}})
        }

        const handlePage = (obj) => {
            handleMyComment(obj)
        }

        onMounted(() => {
            handleMyComment();
        })

        return {
            state,
            moment,
            handleSelectOneComment,
            handleSelectOneCours,
            handlePage
        }
    }
}
</script>

<style lang="css" scoped>
.my-comment-title:hover {
    cursor: pointer;
    text-decoration: underline;
}
.my-comment-coursname:hover {
    cursor: pointer;
    text-decoration: underline;
}
.head-text {
    font-weight: bolder;
}
</style>