<template>
    <div class="b_wrap">
      <div class="b_article">
        <div class="b_art_header">
          <input v-model="state.title"/>
          <select v-model="state.category">
              <option>자유</option>
              <option>질문</option>
              <option>중고거래</option>
          </select>    
        </div>
        <hr />
        <editor v-model="state.content" />
    
        <div class="b_art_footer">
          <q-btn class="b_art_footer_btn_update" flat style="background: #9286B5; color: white; width: 80px;" @click="updateBoard" icon="edit" size="20px" />
          <q-btn class="b_art_footer_btn_cancel" flat style="background: #B57162; color: white; width: 80px;" @click="cancelBoard" icon="close" size="20px" />
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  import { useRoute, useRouter } from 'vue-router';
  
  import Editor from '@/components/editor/updateboardeditor/UpdateBoardEditor.vue'
  import { computed, onMounted, reactive } from '@vue/runtime-core';
  import { useStore } from 'vuex';
  
  export default {
      components: {
        Editor,
      },
  
      setup () {
        
        const route = useRoute();
        const router = useRouter();
        const store = useStore();

        const state = reactive({
            board : "<p>The Content</p>",
            no : Number(route.query.no),
            title : "",
            category : "자유",
            content : "",
            boardreply : "",
            page : 1,
            total : 1,
            reply : "",
            token : sessionStorage.getItem("TOKEN"),
            logged : computed(() => store.getters.getLogged),
            nickname : "",
            id : "",
        });

        const handleData = async () =>{
            const url = `/COCO/api/board/selectone?no=`+state.no;
            const headers = {"Content-Type" : "application/json"};

            const { data } = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200){
                state.title = data.result.title;
                state.content = data.result.content;
                state.nickname = data.result.nickname;
            }
        };

        const updateBoard = async() =>{
            const url = `/COCO/api/board/update.json`;
            const headers = {
                "Content-Type":"application/json",
                "TOKEN":sessionStorage.getItem("TOKEN")
            };
            const body = {
                  no      : state.no,
                  title   : state.title,
                  content : state.content,
            }
            const {data} = await axios.put(url, body, {headers});
            console.log(data);
            if(data.status === 200){
              router.push({path:'/boardone', query:{no:state.no}});
            }
        };

        const cancelBoard = async() => {
          router.push({path:'/boardone', query:{no:state.no}});
        }

        onMounted(() => {
            handleData();
        })

        return {
          state, 
          handleData, 
          updateBoard, 
          cancelBoard,
          options: [
          '자유', '질문', '중고거래'
        ]}
      }
    }
    
  
  </script>
  
  <style lang="scss">
   .b_wrap {
    padding: 50px;
    display: flex;
    justify-content: center;
  }
  .b_article {
    width: 1000px;
  }
  .b_art_header {
    display: flex;
    justify-content: space-between;
    height: 50px;
  }
  .b_art_header > input {
    width: 600px;
    border: 3px solid #9286B5;
    border-radius: 0.5em;
    font-size: 21px;
  }
  .b_art_header > select {
    width: 150px;
    border: 3px solid #9286B5;
    border-radius: 0.5em;
    font-size: 15px;
    font-weight: 600;
    color: #9286B5;
  }
  .b_art_footer {
    padding-top: 10px;
    display: flex;
    justify-content: flex-end;
    column-gap: 0.5px;
  }

  </style>