<template>
    <div class="container">
        
        <div class="q-pa-md row items-start q-gutter-md">
            <q-card class="my-card" flat>

            <q-card-section>
                <div class="text-h6 text-secondary">외부링크 추가</div>
            </q-card-section>
            <div>
                <div>
                    <q-card flat>
                        <q-item>
                            <q-item-section class="vfor-box">
                                <div v-for="tmp of state.linkList" :key="tmp" class="row" style="padding: 15px;">
                                    <q-card class="row">
    
                                        <q-card-section class="row" style="width:450px; ">
                                            <div hidden>{{tmp.linkno}}</div>
    
                                            <div class="link-box" @click="handleHref(tmp.platform, tmp.url)">
                                                <img class="link-img" :src="handleIcon(tmp.platform)" />
                                            </div>
    
                                            <div>{{tmp.platform}}</div>
                                            <div>.com/</div>
                                            <div style="width:160px;">{{tmp.url}}</div>
                                        </q-card-section>
    
                                        <q-separator vertical />
    
                                        <q-card-actions>
                                            <div><q-btn color="negative" flat dense @click="handleDeleteLink(tmp.linkno)" label="삭제" /></div>
                                        </q-card-actions>
    
                                    </q-card> 
                                </div>
                            </q-item-section>
                        </q-item>
                    </q-card>
                </div>
    
                <q-card-actions>
                    <q-btn class="btn-frame" dense color="white" text-color="black" label="뒤로" @click="handlePrev" />
                    <q-btn class="btn-frame" dense color="primary" label="신청" @click="handleApply" />
    
                    <q-space />
    
                    <q-btn color="grey" flat dense label="외부링크 추가" :icon="expanded ? 'keyboard_arrow_up' : 'keyboard_arrow_down'" @click="expanded = !expanded" />
                </q-card-actions>
            </div>

            <q-slide-transition>
                <div v-show="expanded">
                    <div class="q-pa-md row">
                        <q-select style="width:90px;" color="secondary" class="input-item" options-dense dense v-model="state.platform"
                        option-value="value" option-label="label" emit-value map-options :options="options" label="플랫폼" />
                        <q-input style="width:160px;" color="secondary" dense v-model="state.link.url" class="input-item" autofocus label="주소" />
                        <q-btn flat dense color="primary" label="추가" @click="handleInsertLink" />
                    </div>
                </div>
            </q-slide-transition>
            </q-card>
        </div>

        

    </div>
</template>

<script>
import { reactive, ref } from '@vue/reactivity'
import { useRouter } from 'vue-router'
import axios from 'axios';
import { onMounted } from '@vue/runtime-core';
import {fasFont} from '@quasar/extras/fontawesome-v6'
export default {
    setup() {
        const router = useRouter();
        const state = reactive({
            token : sessionStorage.getItem("TOKEN"),
            link : [],
            linkList : [],
            teacher : [],
            platform : []
        })

        const handleTeacherData = async() => {
            const url = `/COCO/api/teacher/selectOneteacher.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            }
            const { data } = await axios.get(url, {headers});
            if(data.status === 200) {
                state.teacher = data.result
                // state.category = data.result.category;
                // state.intro = data.result.intro;
                // state.steps = data.result.steps;
            }
        }

        const handleIcon = (obj) => {
            let src = require(`@/assets/svg/icon/${obj}.svg`)
            return src
        }

        const handlePrev = () => {
            router.push({path : '/tchredu'})
        }

        const handleApply = async() => {
            const url = `/COCO/api/teacher/updateteacher.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            };
            const body = {
                category : state.teacher.category,
                intro : state.teacher.intro,
                steps : 1
            }
            const { data } = await axios.post(url, body, {headers});
            console.log(data)
            if(data.status === 200) {
                alert('신청이 완료 되었습니다.');
                router.push({path: '/main'});
            }
        }

        const handleHref = (platform, url) => {
                window.open(`https://${platform}.com/${url}`)
        }

        // ----------------------------------------------------------------링크등록-----------------------------------------------------
        const handleInsertLink = async() => {
            if(state.linkList.length >= 3 ){
                alert('외부링크는 최대 3개까지만 추가가능합니다.')
            }
            else {
                const url = `/COCO/api/teacher/joinlink.json`;
                const headers = {
                    "Content-Type" : "application/json",
                    "TOKEN" : state.token
                };
                const body = {
                    platform : state.platform,
                    url : state.link.url,
                }
                
                const { data } = await axios.post(url, body, {headers});
                console.log(data)
                if(data.status === 200) {
                    handleLinkData();
                }
            }
        }
        
        // ----------------------------------------------------------------링크조회-----------------------------------------------------
        const handleLinkData = async() => {
            const url = `/COCO/api/teacher/selectListlink.json`;
            const headers = {
                "Content-Type" : "application/json",
                "TOKEN" : state.token
            }
            const { data } = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200) {
                state.linkList = data.link;
            }
        }

        // ----------------------------------------------------------------링크 삭제-----------------------------------------------------
        const handleDeleteLink = async(linkno) => {
            const url = `/COCO/api/teacher/deletelink.json?linkno=${linkno}`;
            const headers = {
                "Content-Type" : "application/json", "TOKEN" : state.token
            };
            if(confirm('삭제하시겠습니까?')){
                const {data} = await axios.post(url, {}, {headers});
                console.log(data);
                handleLinkData();
            }
        }

        onMounted(() => {
            handleTeacherData();
            handleLinkData();
        })

        return {
            state,
            fasFont,
            handleLinkData,
            handleInsertLink,
            handleDeleteLink,
            handlePrev,
            handleApply,
            handleTeacherData,
            handleIcon,
            handleHref,
            expanded: ref(false),
            platform: ref(null),
            options : [
                {
                    label : 'youtube',
                    value : 'youtube',
                    srcUrl : require('@/assets/svg/icon/youtube.svg')
                },
                {
                    label : 'instagram',
                    value : 'instagram',
                    srcUrl : require('@/assets/svg/icon/instagram.svg')
                },
                {
                    label : 'twitch',
                    value : require('@/assets/svg/icon/twitch.svg')
                },
                {
                    label : 'facebook',
                    value : 'facebook',
                    srcUrl : require('@/assets/svg/icon/facebook.svg')
                }
                // 'youtube','twitch','instagram','facebook'
            ]
        }
    }
}
</script>

<style lang="css" scoped src="@/assets/css/TeacherPage.css">

</style>