<template>
    <div class="main-box">

    </div>

    <!-- 강좌 리스트 -->
    <div class="row" v-show="showData">

        <div class="col-2"></div>
        
        <div class="col-8">

            <div class="q-pa-md row">
                <q-card class="filter row">
    
                    <q-btn align="left" style="width:360px;" :ripple="false" flat icon="tune" label="필터" />
            
                    <q-menu style="width:360px;">
    
                        <q-card-section class="check-box row">
    
                            <q-checkbox color="secondary" dense v-model="state.chk" val="&name=Blues" label="Blues" />
                            <q-checkbox color="secondary" dense v-model="state.chk" val="&name=Jazz" label="Jazz" />
                            <q-checkbox color="secondary" dense v-model="state.chk" val="&name=Rock" label="Rock" />
                            <q-checkbox color="accent" dense  v-model="state.chk" val="&name=음악이론" label="음악이론" />
                            
                            <q-checkbox color="secondary" dense v-model="state.chk" val="&name=R&B" label="R&B" />
                            <q-checkbox color="secondary" dense v-model="state.chk" val="&name=Funk" label="Funk" />
                            <q-checkbox color="secondary" dense v-model="state.chk" val="&name=Hip-Hop" label="Hip-Hop" />
                            <q-checkbox color="accent" dense v-model="state.chk" val="&name=개별곡" label="개별곡" />
                            
                            <q-checkbox color="secondary" dense v-model="state.chk" val="&name=Pop" label="Pop" />
                            <q-checkbox color="secondary" dense v-model="state.chk" val="&name=Classic" label="Classic" />
                            <q-checkbox color="secondary" dense v-model="state.chk" val="&name=EDM" label="EDM" />
                            <q-checkbox color="accent" dense v-model="state.chk" val="&name=컴퓨터음악" label="컴퓨터음악" />
    
                        </q-card-section>

                        <q-card-section style="margin-top:-20px;">
                            <q-select color="secondary" options-dense dense outlined style="width:136px;" emit-value map-options label="분류"
                                v-model="state.inst" :options="options" :option-value="options.value" :option-label="options.label" />
                        </q-card-section> 
    
                        <q-separator />
    
                        <q-card-actions class="row">

                            <q-input color="secondary" square outlined v-model="state.text" placeholder="검색어를 입력하세요." dense style="width:168px;">
                                    <template v-slot:append>
                                        <q-icon v-if="state.text !== ''" name="close" @click="state.text = ''" class="cursor-pointer" />
                                    </template>
                            </q-input>

                            <q-space />

                            <q-btn flat label="초기화" @click="handleReset" style="width:60px;" />
                            
                            <q-btn color="secondary" label="조회"  @click="handleSearch" style="width:60px;" v-close-popup />

                        </q-card-actions>
    
                    </q-menu>
    
                </q-card>

                <q-space />
    
                <div style="text-align:right">
                    <q-btn-dropdown style="display:inline-block" dense color="secondary" flat :label=state.sort>
                        <q-list>
        
                            <q-item clickable dense v-close-popup @click="handleSort(1, 1, '최신순')">
                                <q-item-section>
                                    <q-item-label>최신순</q-item-label>
                                </q-item-section>
                            </q-item>
    
                            <q-item clickable dense v-close-popup @click="handleSort(5, 1, '인기순')">
                                <q-item-section>
                                    <q-item-label>판매순</q-item-label>
                                </q-item-section>
                            </q-item>
                            <q-item clickable dense v-close-popup @click="handleSort(2, 1, '평점순')">
                                <q-item-section>
                                    <q-item-label>평점순</q-item-label>
                                </q-item-section>
                            </q-item>
                            <q-item clickable dense v-close-popup @click="handleSort(4, 1, '리뷰많은순')">
                                <q-item-section>
                                    <q-item-label>리뷰많은순</q-item-label>
                                </q-item-section>
                            </q-item>
        
                            <q-item clickable dense v-close-popup @click="handleSort(3, 1, '가격높은순')">
                                <q-item-section>
                                    <q-item-label>가격높은순</q-item-label>
                                </q-item-section>
                            </q-item>
        
                            <q-item clickable dense v-close-popup @click="handleSort(3, 2, '가격낮은순')">
                                <q-item-section>
                                    <q-item-label>가격낮은순</q-item-label>
                                </q-item-section>
                            </q-item>
        
                        </q-list>
                    </q-btn-dropdown>
                </div>

            </div>

            <div v-if="state.result" class="q-pa-md text-center text-body1">
                <div class="text-h6">검색 결과가 없습니다.</div>
                <q-btn dense color="secondary" label="뒤로" @click="handleBack" style="width:68px;" />
            </div>
            
            <div v-if="!state.result">

            <!-- 목록부분 -->
            <div>
                <q-intersection once transition="scale" v-for="(tmp, idx) in state.courslist" :key="idx" style="display: inline-block;vertical-align: middle;">
                    <q-card flat class="cours-card" align="center" @click="SelectOne(tmp.coursno)" style="margin-top:50px;">
                        <q-card-section class="cours-img" align="center">
                            <img :src="`/COCO/api/cours/image?no=${tmp.imageno}`" class="img-src" :ratio="4/3" />
                        </q-card-section>

                        <div align="left" class="cours-info" @click="SelectOne(tmp.coursno)">
                                
                                <div class="text-h6 hov title" style="height: 45px; line-height: 120%;">{{tmp.title}}</div>
                                
                                <div class="hov tchr text-overline">{{tmp.teachername}}</div>
                                <div class="row">
                                    <q-rating class="rating-box" v-model="tmp.ratingavg" size="1.5em" no-dimming icon="star_border" icon-selected="star" readonly  /> 
                                    <span class="hov" v-if="tmp.reviewtotal !=0">({{tmp.ratingavg}})</span>
                                </div>
                                
                                <div class="price text-weight-bold text-subtitle1 hov">₩{{tmp.price}}</div>


                                <div class="tag-box">
                                    
                                    <br />
                                    <div class="inst-box row">
                                        <q-icon style="position:relative; top:2px;" size="1.2em" name="equalizer" />
                                        <span>&nbsp;{{tmp.instrument}}&nbsp;/</span>
                                        <span>&nbsp;{{tmp.difficult}}</span>
                                    </div>

                                    <div v-for="tmp1 in tmp.name" :key="tmp1" class="row tag-for" style="display:inline-block;">
                                        <q-chip clickable class="bg-white" icon="music_note" square dense outline color="secondary" text-color="white">{{tmp1}}</q-chip>
                                    </div>
                                </div>                            
                        </div>
                    </q-card>
                </q-intersection>

            </div>

            <br />

            <div style="text-align:center">
                <q-btn class="btn-more" no-caps outline dense color="secondary" @click="handleMore" label="read more" />
            </div>


            
        </div>

        </div>

        <div class="col-2"></div>
        
    </div>

    <q-inner-loading :showing="visible" >
        <q-spinner-facebook color="secondary" size="50px" />
    </q-inner-loading>

  </template>
  
  <script>
import { reactive, ref, toRefs } from '@vue/reactivity'
import axios from 'axios'
import { computed, onMounted } from '@vue/runtime-core'
import router from '@/routes'
import { useStore } from 'vuex'
export default {
    methods : {
        SelectOne (obj) {
            router.push({path:'/coursone', query:{coursno : obj}})
        }
    },
      setup() {
        const store = useStore();
        const state = reactive({
            token : sessionStorage.getItem("TOKEN"),
            courslist : [],
            data : [],
            mainText : computed(() => store.getters.getMainText),
            categorySearch : computed(() => store.getters.getCategorySearch),
            page : 1,
            text : '',
            opt : 1,
            row : 1,
            inst : '',
            // price : [],
            chk : [],
            sort : '정렬',
            result : false,
            cate : '',
        })

        const visible = ref(false)
        const showData = ref(false)

        const handleCoursData = async() => {
            visible.value = true;
            showData.value = false;
            console.log('스토어 가져온 값 => ', state.mainText)
            console.log('태그로 검색 =>', state.categorySearch)
            
            let url = `/COCO/api/cours/findcours.json?page=${state.page}&text=${state.text}`;
            // GNB에서 검색할 때
            if(state.mainText !== null) {
                url = `/COCO/api/cours/findcours.json?page=${state.page}&text=${state.mainText}`;
            }
            // selectone에서 태그 눌러서 서칭
            else if(state.categorySearch !== null) {
                url=`/COCO/api/cours/findcours.json?=${state.page}&text=${state.text}&name=${state.categorySearch}`
            }            
            const headers = {"Content-Type" : "application/json"}
                const { data } = await axios.get(url, {headers})
                if(data.status === 200) {
                    for(let i=0;i<data.courslist.length;i++){
                        data.courslist[i].price = data.courslist[i].price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
                    }
                    state.courslist = data.courslist
                    state.data = data
                }
                else if(data.status === 0) {
                    state.result = true;
                }
                console.log(data);
                store.commit('setMainText', null);
                store.commit('setCategorySearch', null);
                visible.value = false;
                showData.value = true;
        }
        
        const handleSearch = async() => {
            visible.value = true;
            showData.value = false;
            state.page = 1;
            let str = state.chk.join("");
            const url = `/COCO/api/cours/findcours.json?page=${state.page}
                        &text=${state.text}&opt=${state.opt}&row=${state.row}`+state.inst+str;

            const headers = {"Content-Type" : "application/json"};
            console.log("URL => ", url )
            const { data } = await axios.get(url, {headers})
            console.log(data)
            if(data.status === 0) {
                state.result = true;
            }
            else if(data.status === 200) {   
                for(let i=0;i<data.courslist.length;i++){
                    data.courslist[i].price = data.courslist[i].price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
                }
                state.courslist = data.courslist;
                state.result = false;
            }
            visible.value = false;
            showData.value = true;
        }

        const handleSearchMore = async() => {
            let str = state.chk.join("");
            const url = `/COCO/api/cours/findcours.json?page=${state.page}
                        &text=${state.text}&opt=${state.opt}&row=${state.row}`+state.inst+str;

            const headers = {"Content-Type" : "application/json"};
            console.log("URL => ", url )
            const { data } = await axios.get(url, {headers})
            console.log(data)
            if(data.status === 0) {
                alert('더이상 데이터가 없습니다.')
                state.page--;
            }
            if(data.status === 200) {   
                for(let i=0;i<data.courslist.length;i++){
                    data.courslist[i].price = data.courslist[i].price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
                }
                state.courslist = state.courslist.concat(data.courslist);
            }
        }
        
        const handleSort = async (opt, row, sort) => {
            state.opt = opt;
            state.row = row;
            state.sort = sort;
            await handleSearch();
        }
        
        const handleMore = () => {
            state.page++;
            handleSearchMore();
        }

        const handleReset = () => {
            state.text = '';
            state.inst = '',
            state.chk = [],
            state.page = 1
        }

        const handleBack = () => {
            state.text = '';
            state.inst = '',
            state.chk = [],
            state.page = 1
            handleSearch();
        }

        onMounted(() => {
            handleCoursData();
            // handleCarouselData();
        })
  
        return {
            state,
            visible,
            showData,
            slide: ref(1),
            autoplay: ref(true),
            // expanded: ref(false),
            onItemClick () {},
            handleSort,
            handleSearch,
            handleMore,
            handleSearchMore,
            handleReset,
            handleBack,
            // inst : ref('전체'),
            options : [
                {label : '전체',value : ''},
                {label : '보컬(남)',value : '&inst=보컬(남)'},
                {label : '보컬(여)',value : '&inst=보컬(여)'},
                {label : '일렉트릭 기타',value : '&inst=일렉트릭 기타'},
                {label : '베이스 기타',value : '&inst=베이스 기타'},
                {label : '어쿠스틱 기타',value : '&inst=어쿠스틱 기타'},
                {label : '신디사이저',value : '&inst=신디사이저'},
                {label : '피아노',value : '&inst=피아노'},
                {label : '드럼',value : '&inst=드럼'},
                {label : 'MIDI',value : '&inst=MIDI'},
                {label : 'ETC',value : '&inst=ETC'},
            ],
            ...toRefs(state)
        }
    }
}
  </script>
  
  <style lang="css" scoped >
    @import "@/assets/css/CoursFor.css";
    @import "@/assets/css/CoursMain.css";
  </style>