<template>
    <div class="q-pa-md row items-start q-gutter-md">
        <q-card class="my-card" flat>
            <q-card-section>
            <div class="text-h6 text-secondary" style="margin-top: 50px; margin-left: 300px;">글쓰기</div>
        </q-card-section>

        <div class="q-pa-md" style="margin-top:10px;">
            <q-input outlined v-model="state.title" label="제목" style="width: 689px;" />
            <q-select outlined v-model="state.type" :options="options" label="유형" style="width: 689px;"/>
            <q-input outlined type="textarea" color="secondary" v-model="state.content" label="내용" 
            style="height: 540px, width : 850px, margin-top:-20px;" counter maxlength="1000" /><br />
            <q-btn-group push>
                <q-btn style="background: #74b59d; color: white" push label="뒤로가기" @click="handleReturn" />
                <q-btn style="background: #74b59d; color: white" push label="입력" @click="handleInsert" />
            </q-btn-group>
        </div>
        </q-card>

    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import axios from 'axios';
import { useRouter } from 'vue-router';
export default {
    setup () {
        const router = useRouter();
        const state = reactive({
            title : '',
            type : '',
            content : '',
            token : sessionStorage.getItem("TOKEN")
        });

        const handleInsert = async () => {
            const url = `/COCO/api/qna/insert.json`;
            const headers = {"Content-Type":"application/json", "token" : state.token};
            const body = {title : state.title, type : state.type, content : state.content};
            const {data} = await axios.post(url, body, {headers});
            console.log(data);
            if(data.status === 200){
                alert('등록 완료');
                router.push({path : '/contact'});
            }
        };
        
        const handleReturn = async () => {
            router.push({path : '/contact'});
        };

        return {state, handleInsert, handleReturn, options: ['결제', '강좌', '강사', '계정']};
    }
}
</script>

