<template>
    <div class="row" style="margin-top:100px;">

        <div class="col-4"></div>
        <div class="col-4">
            <div class="tc-head">
                <div v-if="state.memberImage.no != null">
                    <q-img :src="`/COCO/api/member/image?no=${state.memberImage.no}`" 
                    class="tc-head-img blur" fit="fill" />
                </div>
                <div v-if="state.memberImage.no == null">
                    <q-img :src="state.avatar" class="tc-head-img blur" fit="fill" />
                </div>
            </div>
            <q-card>
                <div class="tc-card">

                    <div class="avatar up-up flex flex-center">
    
                        <div v-if="state.memberImage.no != null" class="tc-avatar-box" >
                                <q-avatar class="avatar-member" size="100px" >
                                    <img :src="`/COCO/api/member/image?no=${state.memberImage.no}`"/>
                                </q-avatar>
                            </div>
                        
                        <div v-if="state.memberImage.no == null" class="tc-avatar-box">
                                <q-avatar class="avatar-member" size="100px">
                                    <img :src="state.avatar" />
                                </q-avatar>
                        </div>
        
                    </div>
                    <div class="text-h5 text-center" style="margin-top:-30px;">{{state.memberInfo.name}}</div>
    
                    <div align="center">
    
                        <div v-for="tmp of state.data.link" :key="tmp" style="display:inline-block;margin: 10px;">
                            
                            <div class="tc-link-box"  @click="handleHref(tmp.platform, tmp.url)">
                                <img class="tc-link-img" :src="handleIcon(tmp.platform)"/>
                            </div>
                            
                        </div>
                    </div>
                    
                    <q-separator />

                        <q-card-actions align="center">
                            <q-btn flat label="FOLLOW" style="width:48%; margin-right:1.75%"/>
                            <q-separator vertical />
                            <q-btn flat label="SEND MESSAGE" style="width:48%; margin-left:1.75%;" @click="sendMessage()" />
                        </q-card-actions>

                    <q-separator />
                    
                    <div v-for="tmp of state.cours" :key="tmp" class="row flex flex-center">
                        <q-card flat class="row cours-select" style="background:transparent;width: 90%;" @click="handleCours(tmp.coursno)">

                            <q-card-section class="row" >
                                <div hidden>{{tmp.educationno}}</div>
                                <div class="text-overline" >{{tmp.title}}</div>
                            </q-card-section>
        
                        </q-card> 
                    </div>

                </div>

            </q-card>
            

        </div>

        <div class="col-4"></div>
    </div>
</template>

<script>
import { reactive, ref, toRefs } from '@vue/reactivity'
import { useRoute, useRouter } from 'vue-router'
import axios from 'axios';
import { onMounted } from '@vue/runtime-core';
export default {
    setup() {
        const route = useRoute();
        const router = useRouter();
        const state = reactive({
            teacherno : Number(route.query.teacherno),
            data : [],
            data1 : [],
            cours : [],
            memberInfo : [],
            memberImage : [],
            avatar : require("@/assets/images/logo/coconut_500.png"),
            ganpan : '',
            teacher : [],
            image : []
        });

        const handleData = async() => {
            const url = `/COCO/api/teacher/selectoneno.json?teacherno=${state.teacherno}`;
            const headers = {"Content-Type":"application/json"}
            const { data } = await axios.get(url, {headers:headers});
            console.log(data);
            state.data = data;
            state.teacher = data.teacher;
            state.image = data.image;
        };

        const handleDataCours = async() => {
            const url = `/COCO/api/teacher/selectcours.json?teacherno=${state.teacherno}`;
            const headers = {"Content-Type":"application/json"};
            const {data} = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200){
                state.data1 = data
                state.cours = data.cours;
            }
        };

        const sendMessage = () => {
            router.push({path: '/chatcontent', query: {receiver : state.memberInfo.id}})
        }

        const handleHref = (platform, url) => {
                window.open(`https://${platform}.com/${url}`)
        }

        const handleIcon = (obj) => {
            let src = require(`@/assets/svg/icon/${obj}.svg`)
            return src
        }

        const handleMemberImage = async() => {
            const url = `/COCO/api/teacher/selectmember.json?teacherno=${state.teacherno}`;
            const headers = {
                "Content-Type":"application/json"
            };
            const { data } = await axios.get(url, {headers});
            console.log('웨않됨?', data)
            if(data.status === 200) {
                state.memberInfo = data.teacher;
                state.memberImage = data.image;
            }
        }

        const handleCours = (obj) => {
            router.push({path:'/coursone', query:{coursno : obj}})
        }

        onMounted(() => {
            handleData();
            handleDataCours();
            handleMemberImage();
        });

        return {
            state,
            handleIcon,
            handleHref,
            handleCours,
            sendMessage,
            slide:ref(1),
            ...toRefs(state),
            options : [
                {
                    label : 'youtube',
                    value : 'youtube',
                    srcUrl : require('@/assets/svg/icon/youtube.svg')
                },
                {
                    label : 'instagram',
                    value : 'instagram',
                    srcUrl : require('@/assets/svg/icon/instagram.svg')
                },
                {
                    label : 'twitch',
                    value : require('@/assets/svg/icon/twitch.svg')
                },
                {
                    label : 'facebook',
                    value : 'facebook',
                    srcUrl : require('@/assets/svg/icon/facebook.svg')
                }
                // 'youtube','twitch','instagram','facebook'
            ]
        };
    }
}
</script>

<style lang="css" scoped>
@import url("@/assets/css/TeacherPage.css");
@import url("@/assets/css/App.css");
@import url("@/assets/css/TeacherSelectOne.css");

.up-up {
     transform: translateY(-40px);
}
.cours-select:hover {
    cursor: pointer;
}
</style>