<template>
    <div>
      {{state}}
    </div>
  </template>
  
  <script>
  import { reactive } from '@vue/reactivity';
  import { onMounted } from '@vue/runtime-core';
  import axios from 'axios';
  import { useRouter } from 'vue-router';
  import { useStore } from 'vuex';
  export default {
    // mounted() {
    //   const naver_id_login = new window.naver_id_login("YGknk7lCNc_ce4kKjir0", "http://localhost:9090/ROOT/vue/#/login/naver");
    //   console.log("access token =", naver_id_login.getAccessToken()); // 정상적 로그인이 된 경우 access token값 출력
    // },
    setup () {
      const store = useStore();
      const router = useRouter();
      const naver_id_login = new window.naver_id_login("YGknk7lCNc_ce4kKjir0", "http://3.35.91.60:8080/COCO/login/naver");
      const state = reactive({
        token : naver_id_login.getAccessToken()
      });
  
      onMounted(() => {
        handleLogin();
        router.push({path:'/'});
      });
  
      const handleLogin = async() => {
              // 유효성 검사
  
              const url = '/COCO/api/member/naverlogin.json';
  
              const headers = {"Content-Type":"application/json"};
              const body = {
                  token : state.token
              }
  
              const { data } = await axios.post(url, body, {headers});
              console.log(data);
              if(data.status === 200){
                  console.log('완료');
                  sessionStorage.setItem("TOKEN", data.result);
                  store.commit('setLogged', true);
              }
              else {
                  console.log('실패');
              }
        }
  
      return {state}
    }
  };
  </script>