<template>
    <div>
        
    </div>
</template>

<script>
import { reactive } from "@vue/reactivity";
import axios from 'axios';
import { useRoute } from 'vue-router';
import { onMounted } from '@vue/runtime-core';

export default {
    setup(){
        const route = useRoute();
        const state = reactive({
            orderno : route.query.orderno
        })

       
        const handleData = async() => {
            const url = `/COCO/api/kakao/kakaoPay.json?orderno=${state.orderno}`;
            const headers = {
                "Content-Type":"application/json"
            };
            const { data } = await axios.post(url,{}, {headers})
            console.log(data)
            if (data === '/') {
                alert('결제오류로 인하여 다시 시도하여주시기바랍니다')

                console.log("없엉");
            }
            else{
                // window.open(data, '새창의 Title', 'height=400, width=300, resizable=yes, scrollbars=yes')
                // window.close()
                window.location.href = (data);
            }
        }
        onMounted(()=>{
            handleData()
        })

        return {state, handleData}
    }
}
</script>

