<template>
  <div class="container">
    
    <editor v-model="content" />

    <div class="content">
      <h5>HTML</h5>
      <pre><code>{{ content }}</code></pre>
    </div>

    <q-btn outline @click="writeBoard">
      글쓰기
    </q-btn>
  </div>
</template>

<script>
import axios from 'axios';

import Editor from '@/components/editor/boardeditor/BoardEditor.vue'

export default {
    components: {
      Editor,
    },

    data() {
      return {
        content : '<p></p><p></p><p></p><p></p><p></p><p></p><p></p><p></p>',
      }
    },

    methods : {
      writeBoard : async function(){
        console.log(this.content);
        const url = `/COCO/api/board/insert.json`;
            const headers = {
              "Content-Type":"application/json",
              "TOKEN":sessionStorage.getItem("TOKEN")
            };
            const body = {
                title   : "title",
                content : this.content,
            }
            const {data} = await axios.post(url, body, {headers});
            console.log(data);
      },
    },
  }
  

</script>

<style lang="scss">
.container {
  padding: 50px;
}
</style>