<template>

    <div v-if="state.yesData" class="q-pa-md">
        <q-markup-table flat wrap-cells style="margin-top:12px;width:100%">
            <thead class="bg-accent text-white">
                <tr style="height:20px;">
                    <th class="text-center head-text">내용</th>
                    <th class="text-right head-text">받은 사람</th>
                    <th class="text-right head-text">보낸 시간</th>
                </tr>
            </thead>
            <tbody>
                <tr @click="handleSelectOneMessage(tmp.chatno)" v-for="(tmp, idx) in state.chat" :key="idx" class="msg-rows">
                    <td width="63%" class="text-left body-text">
                        <div style="position:relative">&nbsp;
                            <div class="td-ellipsis" style="">{{tmp.content}}</div>
				</div>
                    </td>
                    <td class="text-right">{{tmp.receiver}}</td>
                    <td class="text-right">{{moment(tmp.regdate).format("MM-DD HH:mm")}}</td>
                </tr>
            </tbody>
        </q-markup-table>
        <div class="q-pa-lg flex flex-center" >
            <q-pagination color="accent" v-model="state.page" :max-pages="5"
                :boundary-numbers="false" :ellipses="false" @click="handlePage(state.page)"
                :max="state.total"/>
        </div>
    </div>

    <div v-if="state.total === 0" style="margin-top:-30px;">
        <div class="text-body1 flex flex-center">메시지가 없습니다.</div>
    </div>

    <!-- 메시지 1개 선택조회 모달 -->
    <q-dialog v-model="state.modalChat">
        <q-card style="width:300px;">

                <q-card-section class="row">
                    <span class="text-grey"> 받은 사람 : </span>
                    <span>&nbsp;{{state.chatOne.receiver}}</span>
                </q-card-section>
                
                <q-separator />

                <q-card-section style="min-height:200px;">
                    <q-item-section>
                        {{state.chatOne.content}}
                    </q-item-section>
                </q-card-section>

                <q-separator />

                <q-card-actions align="right">
                    <!-- <q-btn dense flat color="negative" @click="handleDelete(state.chatOne.chatno)" label="삭제" /> -->
                    <q-btn dense color="primary"  label="다시 보내기"
                        @click="handleContent(state.chatOne.receiver)" />
                </q-card-actions>
                
        </q-card>
    </q-dialog>
</template>

<script>
import { reactive } from '@vue/reactivity'
import axios from 'axios'
import { onMounted } from '@vue/runtime-core'
import { useRouter } from 'vue-router'
export default {
    setup () {
        const router = useRouter();
        const moment = require('moment')
        const state = reactive({
            token : sessionStorage.getItem("TOKEN"),
            chat : [],
            yesData : true,
            chatOne : [],
            modalChat : false,
            page : 1,
            total : 1
        })

        const handleMessageData = async() => {
            const url = `/COCO/api/chat/selectsendertoken.json?page=${state.page}`;
            const headers = {"Content-Type":"application/json", "TOKEN" : state.token};
            const {data} = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200) {
                // data.chat1.sort(function(a, b){
                //     return b.chatno - a.chatno;
                // });
                state.chat = data.chat1;
                state.total = Math.ceil(data.total/10);
                if(data.chat1.total !== 0) {
                    state.yesData = true;
                }
                else {
                    state.yesData = false;
                }
            }
        }

        const handlePage = (obj) => {
            handleMessageData(obj)
        }

        const handleSelectOneMessage = async(obj) => {
            const url = `/COCO/api/solda/selectonechat.json?chatno=${obj}`;
            const headers = {
                "Content-Type":"application/json"
            }
            const { data } = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200) {
                state.chatOne = data.chat;
                state.modalChat = true;
            }
        }

        const handleContent = (obj) => {
            router.push({path: '/chatcontent', query:{receiver : obj}})
        }

        const handleDelete = async(obj) => {
            if(confirm('정말로 삭제하시겠습니까?')) {
                const url = `/COCO/api/chat/delete.json?chatno=${obj}`;
                const headers = {
                    "Content-Type":"application/json",
                    "TOKEN" :state.token
                };
                const {data} = await axios.post(url, {}, {headers});
                console.log(data);
                if(data.status === 200) {
                    alert('메시지가 삭제되었습니다.')
                    state.modalChat = false;
                    handleMessageData();
                }
            }
        }

        onMounted(() => {
            handleMessageData();
        })

        return {
            state,
            moment,
            handleSelectOneMessage,
            handleContent,
            handleDelete,
            handlePage
        }
    }
}
</script>

<style lang="css" scoped>
.head-text {
font-weight: bolder;
}
.td-ellipsis {
    overflow:hidden;
    position:absolute;
    left:0;
    top:0;
    width:100%;
    white-space:nowrap;
    text-overflow:ellipsis;
}
.msg-rows:hover {
    cursor: pointer;
}
</style>