<template>
    <div class="row">
        <div class="col-2"></div>
        <div class="col-8">
            <q-card flat style="width:100%;margin-top:50px;">
                    <q-card-section class="row">
    
                        <q-item-section>
                            <q-item-label class="text-overline text-grey">{{state.coursTitle}}</q-item-label>
                            <q-item-label class="text-bold text-h6">{{state.commentOne.title}}<span v-if="state.secret === 1" class="text-caption text-grey">&nbsp;(비밀글)</span></q-item-label>
                        </q-item-section>
                        
                        <q-item-section v-if="state.userImageno != 0" class="row" side>
                            <q-avatar size="40px" color="white">
                                <q-avatar size="40px">
                                    <img :src="`/COCO/api/member/image?no=${state.userImageno}`" />
                                </q-avatar>
                            </q-avatar>
                        </q-item-section>
                        
                        <q-item-section v-if="state.userImageno == 0" class="row" side>
                            <q-avatar size="40px" color="white">
                                <q-avatar size="40px">
                                    <img :src="state.avatar" />
                                </q-avatar>
                            </q-avatar>
                        </q-item-section>
    
                        <q-item-section side>
                            {{state.userId}}
                        </q-item-section>
                        
                    </q-card-section>

                    <q-separator />

                        <div class="text-right text-grey">{{state.commentOne.regdate}}</div>

                    <q-card-section>
    
                        <q-item-label style="min-height:200px;">
                            {{state.commentOne.content}}
                        </q-item-label>
    
                    </q-card-section>

                    <q-card-actions>
                        <q-btn dense @click="handleBack" label="뒤로" />
                        <q-space />
                        <q-btn v-if="state.writer" dense @click="handleUpdate(state.commentno)" label="수정" />
                        <q-btn v-if="state.writer" color="negative" dense @click="handleDelete(state.commentno)" label="삭제" />
                    </q-card-actions>
    
                    <q-card-section align="center" >
                        <q-markup-table style="width:100%;" flat dense bordered wrap-cells>

                            <tbody v-for="(tmp, idx) in state.recommentList" :key="idx">
                                <tr>
                                    <!-- <td class="text-left">{{((idx+1)+(state.page * 5))-5}}</td> -->
                                    <td class="text-left">{{tmp.userid}}</td>
                                    <td class="text-left" width="60%" style="word-break:break-all">{{tmp.content}}</td>
                                    <td class="text-right">{{moment(tmp.regdate).format("MM-DD HH:mm")}}</td>
                                    <td class="text-right recomment-close" width="1%"><q-icon size="1.3em" color="grey" name="disabled_by_default" /></td>
                                </tr>
                            </tbody>
                        </q-markup-table>
    
                        <div class="q-pa-lg flex flex-center">
                            <q-pagination v-model="state.page" color="secondary"
                                :max="state.total" :max-pages="5"
                                :boundary-numbers="false" @click="handlePage(state.page)"
                            />
                        </div>
                    </q-card-section>

                    <q-card-actions v-if="state.logged" class="flex flex-center" style="margin:-30px 0px 30px 20px;">

                        <q-input type="textarea" autogrow counter maxlength="100" color="secondary"
                            outlined dense v-model="state.reContent" style="width:80%" />

                        <q-btn dense flat @click="handleInsertRecomment(state.commentOne.commentno)"
                            style="margin-top:-20px;" label="댓글달기" text-color="secondary" />

                    </q-card-actions>


                </q-card>
        </div>

        <div class="col-2"></div>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import { useRoute, useRouter } from 'vue-router'
import axios from 'axios';
import { computed, onMounted } from '@vue/runtime-core';
import { useStore } from 'vuex';
export default {
    setup () {
        const router = useRouter();
        const route = useRoute();
        const store = useStore();
        const moment = require('moment');
        const state = reactive({
            token : sessionStorage.getItem("TOKEN"),
            logged : computed(() => store.getters.getLogged),
            page : 1,
            commentno : route.query.commentno,
            userId : '',
            commentOne : [],
            recommentList : [],
            total : 1,
            userImageno : 0,
            avatar : require('@/assets/images/logo/coconut_500.png'),
            reContent : '',
            writer : false,
            coursTitle : '',
            secret : 0,
            coursno : route.query.coursno
        })

        const handleCommentData = async() => {
            const url = `/COCO/api/comment/selectonecom.json?commentno=${state.commentno}&page=${state.page}`;
            const headers = {
                "Content-Type":"application/json"
            };
            const { data } = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200) {
                state.commentOne = data.comment;
                state.userId = data.userid;
                state.recommentList = data.recomment;
                state.total = Math.ceil(data.total/5);
                state.coursTitle = data.courstitle;
                state.secret = data.comment.secret;
                // state.coursno = data.coursno;
                handleMemberImage(data.userid);
            }
        }

        const handleMemberImage = async(obj) => {
            const url = `/COCO/api/solda/userimage.json?userid=${obj}`;
            const headers = {"Content-Type":"application/json"};
            const { data } = await axios.get(url, {headers});
            console.log('유저이미지 => ',data);
            if(data.status === 200) {
                state.userImageno = data.imageno;
            }
            else if (data.status === 0) {
                state.userImageno = 0
            }
        }

        const handleInsertRecomment = async() => {
            if(state.reContent === '') {
                alert('내용을 입력해 주세요.');
                return false;
            }
            if(state.logged === false) {
                alert('로그인이 필요합니다.');
                return false;
            }
            const url = `/COCO/api/recomment/insert.json?commentno=${state.commentno}`
            const headers = {
                "Content-Type":"application/json",
                "TOKEN" : state.token
            }
            const body = {
                content : state.reContent,
                secret : 0
            }
            const { data } = await axios.post(url, body, {headers});
            console.log('코멘트댓글',data)
            if(data.status === 200) {
                state.reContent = '';
                handleCommentData();
            }
        }

        const handleCheckWriter = async() => {
            const url = `/COCO/api/comment/checkmemcom.json?commentno=${state.commentno}`;
            const headers = {
                "Content-Type":"application/json",
                "TOKEN" : state.token
            };
            const { data } = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200) {
                state.writer = true;
            }
        }

        const handleDelete = async(obj) => {
            if(confirm('게시글을 삭제하시겠습니까?')) {
                const url = `/COCO/api/comment/delete.json?commentno=${obj}`;
                const headers = {
                    "Content-Type":"application/json",
                };
                const { data } = await axios.post(url, {}, {headers});
                console.log(data);
                if(data.status === 200) {
                    alert('삭제되었습니다.');
                    history.back();
                }
            }
        }

        const handlePage = (obj) => {
            handleCommentData(obj);
        }

        const handleBack = () => {
            if(route.query.coursno == 0) {
                router.push({path:'/mycours'})
            }
            else if(route.query.no != 0) {
                router.push({path : '/coursone', query : {coursno : state.coursno}})
            }
        }

        const handleUpdate = (obj) => {
            router.push({path:'/commentupdate', query : {commentno : obj}})
        }

        onMounted(() => {
            handleCommentData();
            handleCheckWriter();
        })

        return {
            state,
            moment,
            handleInsertRecomment,
            handlePage,
            handleBack,
            handleUpdate,
            handleDelete
        }
    }
}
</script>

<style lang="css" scoped>
.recomment-close:hover {
    cursor: pointer;
}
</style>