<template>
    <div>

        <div v-if="state.noData" class="q-pa-md text-center text-body1">
            아직 리뷰가 없습니다.
        </div>

        <div v-if="!state.noData">
            <div v-for="tmp in state.reviewList" :key="tmp">
                <q-card style="margin:20px 0;">
    
                    <q-card-section class="row">
    
                        <q-item-section side v-if="tmp.imageno !== 0">
                            <q-avatar size="40px" color="white">
                                <q-avatar size="40px">
                                    <img :src="`/COCO/api/member/image?no=${tmp.imageno}`" />
                                </q-avatar>
                            </q-avatar>
                        </q-item-section>
    
                        <q-item-section side v-if="tmp.imageno === 0">
                            <q-avatar size="40px" color="white">
                                <q-avatar size="40px">
                                    <img :src="state.avatar" />
                                </q-avatar>
                            </q-avatar>
                        </q-item-section>
    
                        <q-item-section side>
                            <q-item-label>{{tmp.userid}}</q-item-label>
                            <q-rating v-model="tmp.rating" size="1.8em" no-dimming icon="star_border" icon-selected="star" readonly  /> 
                        </q-item-section>
    
                    </q-card-section>
    
                    <q-separator />
    
                    <q-card-section>
                        <q-item-label>{{tmp.content}}</q-item-label>
                    </q-card-section>
                </q-card>
            </div>
        </div>


    </div>


</template>

<script>
import { reactive, toRefs } from '@vue/reactivity'
import axios from 'axios'
import { onMounted } from '@vue/runtime-core'
export default  {
    props : {
        title : String,
        token : String,
        cours : Object,
        orderStatus : Number
    },
    setup(props) {
        const state = reactive({
            token : props.token,
            coursno : props.cours.coursno,
            imgno : 0,
            avatar : require('@/assets/images/logo/coconut_500.png'),
            rating : 1,
            content : '',
            reviewList : [],
            noData : false
        })

        const handleReviewData = async() => {
            const url = `/COCO/api/review/selectcours.json?courscode=${state.coursno}`;
            const headers = {
                "Content-Type":"application/json",
            }
            // visible.value = true;
            const { data } = await axios.get(url, {headers});
            console.log(data)
            if(data.status === 200) {
                state.reviewList = data.review;
                state.imgno = data.review.imageno;
                state.noData = false
            }
            else if (data.status === 0) {
                state.noData = true;
            }
        }

        onMounted(() => {
            handleReviewData();
        })

        return{
            state,
            ...toRefs(state)
        }
    }
}
</script>

