<template>
    <div class="row">

        <div class="col-1"></div>
        <div class="col-2" align="right" style="margin-top:20px; margin-left:30px;">

        </div>

        <div class="col-7">

            <q-tabs v-model="tab" dense class="q-pa-md" align="left">
                <q-tab class="text-secondary" name="receive" label="받은 메시지" @click="menu = 1" />
                <q-tab class="text-accent" name="send" label="보낸 메시지" @click="menu = 2" />
            </q-tabs>

            <div align="right" style="margin:-15px 15px;">
                <q-btn dense color="primary" label="메시지 보내기" @click="handleContent" />
            </div>

            <menu-1 v-if="state.menu === 1">

            </menu-1>

            <menu-2 v-if="state.menu === 2">
            
            </menu-2>

            
        </div>

        <div class="col-2"></div>
    </div>
</template>

<script>
import { reactive, ref, toRefs } from '@vue/reactivity'
import axios from 'axios';
import router from '@/routes';
import Menu1 from '@/components/chat/ChatBoxPage.vue';
import Menu2 from '@/components/chat/ChatPostBoxPage.vue';
export default {
    components : {
        Menu1,
        Menu2
    },
    setup () {
        // const route = useRoute();
        const moment = require('moment')
        const state = reactive({
            menu : 1,
            id : '',
            token : sessionStorage.getItem("TOKEN"),
            memberInfo : [],
            content : '',
            text : '',
            text1 : '',
            chat : [],
            chat1 : []
        });

        const handleReset = async () => { // 조회
            // handleReceiverData();
        };

        const handleText = async () => {
            const url = `/COCO/api/chat/selectsender.json?sender=${state.text}`;
            const headers = {"Content-Type":"application/json", "TOKEN" : state.token};
            const {data} = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200){
                state.chat = data.chat;
            }
        };

        const handleContent = () => {
            router.push({path: '/chatsend'})
        }

        return {state,
            handleContent,
            handleText,
            handleReset,
            tab:ref('receive'),
            ...toRefs(state),
            moment
        };
    }
}
</script>

