                        </div>
                    </div>
                </q-card-section>
                
            </q-card>
        </q-dialog> -->


    </div>
</template>

<script>
import { reactive, ref, toRefs } from '@vue/reactivity'
import { useRouter } from 'vue-router'
import axios from 'axios';
import { computed, onMounted } from '@vue/runtime-core';
import { useStore } from 'vuex';
export default {
    props : {
        title : String,
        cours : Object
    },
    setup (props) {
        const router = useRouter();
        const store = useStore();
        const moment = require('moment');
        const state = reactive({
            logged : computed(() => store.getters.getLogged),
            token : sessionStorage.getItem("TOKEN"),
            coursno : props.cours.coursno,
            comment : [],
            modalComment : false,
            commentOne : [],
            userId : '',
            userImageno : 0,
            avatar : require('@/assets/images/logo/coconut_500.png'),
            recomment : [],
            reContent : '',
            page : 1,
            total : 0,
            noData : false,
            reCommentCount : [],
            commentPage : 1,
            recommentTotal : 0
        })

        const handleCommentData = async(obj) => {
            const url = `/COCO/api/comment/selectcomment.json?coursno=${state.coursno}&page=${obj}`;
            if(state.token === null) {
                state.token = "null"
            }
            const headers =  {
                "Content-Type":"application/json",
                "TOKEN" : state.token,
            }
            const { data } = await axios.get(url, {headers});
            console.log(data);
            if(data.status === 200) {
                // 백엔드에서 올 때부터 정렬하는거
                data.comment.sort(function(a, b) {
                    return b.commentno - a.commentno;
                });
                state.comment = data.comment;
                state.total = Math.ceil(data.total/10) ;
                state.noData = false
            }
            else if(data.status === 0) {
                state.noData = true;
            }
        }

        const handlePage = async(obj) => {
            handleCommentData(obj)
        }

        // const handleSelectOne = async (obj) => {
        //     const url = `/COCO/api/comment/selectonecom.json?commentno=${obj}&page=${state.commentPage}`
        //     const headers = {"Content-Type":"application/json"};
        //     const { data } = await axios.get(url, {headers});
        //     console.log(data);
        //     if(data.status === 200) {
        //         state.commentOne = data.comment;
        //         state.userId = data.userid;
        //         state.modalComment = true;
        //         state.recomment = data.recomment;
        //         state.recommentTotal = Math.ceil(data.total/5);
        //         handleMemberImage(data.userid);
        //     }
        // }

        const handleSelectOne = (obj) => {
            router.push({path: '/commentone', query : {commentno : obj, coursno : state.coursno}})
        }

        // const handleMemberImage = async(obj) => {
        //     const url = `/COCO/api/solda/userimage.json?userid=${obj}`;
        //     const headers = {"Content-Type":"application/json"};
        //     const { data } = await axios.get(url, {headers});
        //     console.log('유저이미지 => ',data);
        //     if(data.status === 200) {
        //         state.userImageno = data.imageno;
        //     }
        //     else if (data.status === 0) {
        //         state.userImageno = 0
        //     }
        // }

        const handleInsertRecomment = async(obj) => {
            if(state.reContent === '') {
                alert('내용을 입력해 주세요.');
                return false;
            }
            if(state.logged === false) {
                alert('로그인이 필요합니다.');
                return false;
            }
            const url = `/COCO/api/recomment/insert.json?commentno=${obj}`
            const headers = {
                "Content-Type":"application/json",
                "TOKEN" : state.token
            }
            const body = {
                content : state.reContent,
                secret : 0
            }
            const { data } = await axios.post(url, body, {headers});
            console.log('코멘트댓글',data)
            if(data.status === 200) {
                handleSelectOne(obj);
                state.reContent = '';
            }
        }

        const handleInsertComment = (obj) => {
            router.push({path: '/commentinsert', query : {coursno : obj}})
        }

        const recommentPage = (obj) => {
            handleSelectOne(obj);
        }

        onMounted(() => {
            handleCommentData(state.page);
        })

        return {
            state,
            handleInsertComment,
            ...toRefs(state),
            handleSelectOne,
            userTab : ref(false),
            handleInsertRecomment,
            handlePage,
            recommentPage,
            moment
        }
    }
}
</script>

